import React, { useEffect, useState } from "react";

import { useLocalStorage } from "../hooks/useLocalStorage";
import { checkLoggedIn } from "../api/user";
import { updateCart } from "../api/cart";

export const AuthContext = React.createContext();

function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useLocalStorage("user", null);

  const [cart, setcart] = useState({ products: [], total: 0 });

  const [cartOpen, setcartOpen] = useState(false);

  function checkAuth() {
    checkLoggedIn()
      .then((activeUser) => {
        if (activeUser) {
          setCurrentUser(activeUser.user);
        } else {
          setCurrentUser(null);
        }
      })
      .catch((err) => {
        setCurrentUser(null);
      });
  }

  // useEffect(() => {
  //   if (!discountAdded) {
  //     if (cart.products.length > 0 && cart.total > 10) {
  //       setcart({
  //         ...cart,
  //         total: currentUser?.plan.discountUsed ? cart.total : cart.total - 10,
  //       });
  //       setdiscountAdded(true);
  //     }
  //   }
  // }, [cart.products]);

  const value = {
    currentUser,
    setUser: setCurrentUser,
    cart,
    setcart,
    checkAuth,
    cartOpen,
    setcartOpen,
  };
  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}
export default AuthProvider;
