import React, { useContext, useEffect, useRef, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import NavbarLogo from "../assets/Images/NavbarLogo.png";
import cart_icon from "../assets/Images/cart_icon.png";
import Cart from "./Cart";
import { AuthContext } from "../contexts/userContext";
import { logout } from "../api/user";

function Navbar() {
  const navigate = useNavigate();

  const firstRender = useRef(true);

  const { currentUser, cart, cartOpen, setcartOpen } = useContext(AuthContext);

  const handleLogin = () => {
    navigate("/login");
  };

  const handleLogout = () => {
    logout()
      .then((res) => {
        navigate("/login");
      })
      .catch((err) => {});
  };

  return (
    <div>
      <Cart cartOpen={cartOpen} setcartOpen={setcartOpen} />
      {/* <h6 className="text-center bg-secondary py-1 m-0 font-inter fw-bold xsmall">
        BECOME A MEMBER AND GET $10 OFF ON YOUR FIRST PURCHASE
      </h6> */}
      <nav
        className={`navbar navbar-expand-lg navbar-dark bg-dark px-lg-2 px-xl-5 py-1 my-0`}
        style={{ zIndex: 5 }}
      >
        <div className="container-fluid">
          <Link
            className="nav-logo d-flex align-items-center gap-3 text-decoration-none"
            to="/"
          >
            <img src={NavbarLogo} style={{ height: "50px" }} />
            <p className="m-0 text-white text-decoration-none">
              FISHING THE ATLANTIC
            </p>
          </Link>
          <div className="d-flex">
            <button
              className="navbar-toggler w-25 w-100"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
          </div>
          <div
            className="collapse navbar-collapse position-relative"
            id="navbarSupportedContent"
          >
            <ul
              className="navbar-nav me-auto mb-2 mb-lg-0 justify-content-around m-lg-auto"
              style={{ width: "65%" }}
            >
              <li className="nav-item m-0 p-0">
                <Link className="nav-link m-0 text-white text-nowrap" to="/">
                  <span
                    className="d-block d-lg-none"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                  >
                    HOME
                  </span>

                  <span className="d-none d-lg-block">HOME</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link m-0 text-white text-nowrap"
                  to="/merch"
                >
                  <span
                    className="d-block d-lg-none"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                  >
                    MERCH
                  </span>

                  <span className="d-none d-lg-block">MERCH</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link m-0 text-white text-nowrap"
                  to="/products"
                >
                  <span
                    className="d-block d-lg-none"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                  >
                    PRODUCTS
                  </span>

                  <span className="d-none d-lg-block">PRODUCTS</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link m-0 text-white text-nowrap"
                  to="/membership"
                >
                  <span
                    className="d-block d-lg-none"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                  >
                    MEMBERSHIP
                  </span>

                  <span className="d-none d-lg-block">MEMBERSHIP</span>
                </Link>
              </li>
              <li className="nav-item">
                <Link
                  className="nav-link m-0 text-white text-nowrap"
                  to="/events"
                >
                  <span
                    className="d-block d-lg-none"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                  >
                    EVENTS
                  </span>

                  <span className="d-none d-lg-block">EVENTS</span>
                </Link>
              </li>

              <li className="nav-item">
                <Link
                  className="nav-link m-0 text-white text-nowrap"
                  to="/about"
                >
                  <span
                    className="d-block d-lg-none"
                    data-bs-toggle="collapse"
                    data-bs-target="#navbarSupportedContent"
                    aria-controls="navbarSupportedContent"
                    aria-expanded="false"
                    aria-label="Toggle navigation"
                  >
                    ABOUT
                  </span>

                  <span className="d-none d-lg-block">ABOUT</span>
                </Link>
              </li>
              {!currentUser?.isAdmin ? (
                <li className="nav-item">
                  <Link
                    className="nav-link m-0 text-white text-nowrap"
                    to="/contact"
                  >
                    <span
                      className="d-block d-lg-none"
                      data-bs-toggle="collapse"
                      data-bs-target="#navbarSupportedContent"
                      aria-controls="navbarSupportedContent"
                      aria-expanded="false"
                      aria-label="Toggle navigation"
                    >
                      CONTACT US
                    </span>

                    <span className="d-none d-lg-block">CONTACT US</span>
                  </Link>
                </li>
              ) : (
                <li className="nav-item">
                  <Link
                    className="nav-link m-0 text-white text-nowrap"
                    to="/orders"
                  >
                    <span
                      className="d-block d-lg-none"
                      data-bs-toggle="collapse"
                      data-bs-target="#navbarSupportedContent"
                      aria-controls="navbarSupportedContent"
                      aria-expanded="false"
                      aria-label="Toggle navigation"
                    >
                      ORDERS
                    </span>

                    <span className="d-none d-lg-block">ORDERS</span>
                  </Link>
                </li>
              )}
            </ul>

            <div
              className={`${
                currentUser ? "d-flex" : "d-block"
              } d-lg-flex gap-4 align-items-center px-3 px-lg-0`}
            >
              <div className={`nav-item ${!currentUser ? "mb-4 mb-lg-0" : ""}`}>
                <img
                  data-bs-toggle="collapse"
                  data-bs-target="#navbarSupportedContent"
                  aria-controls="navbarSupportedContent"
                  aria-expanded="false"
                  aria-label="Toggle navigation"
                  className="d-block d-lg-none"
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setcartOpen(!cartOpen);
                  }}
                  src={cart_icon}
                ></img>
                <img
                  className="d-none d-lg-block"
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    setcartOpen(!cartOpen);
                  }}
                  src={cart_icon}
                ></img>
                <span
                  className="small text-white position-absolute bottom-0 mb-1"
                  style={{ marginLeft: "28px" }}
                >
                  {cart?.products?.length > 0 ? cart?.products?.length : ""}
                </span>
              </div>
              <div className="nav-item font-mont">
                {currentUser ? (
                  <div class="dropdown">
                    <button
                      class="btn bg-secondary text-white rounded-circle"
                      type="button"
                      id="dropdownMenuButton1"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                    >
                      {currentUser?.username?.split("")[0].toUpperCase()}
                    </button>
                    <ul
                      class="dropdown-menu bg-muted text-center m-0 mt-1 p-0 nav-dropdown"
                      aria-labelledby="dropdownMenuButton1"
                    >
                      <li
                        style={{
                          borderBottom: "1px solid black",
                        }}
                        className="py-2"
                      >
                        <Link
                          className="text-dark whitespace-nowrap text-decoration-none px-2 text-nowrap"
                          to={"/account-settings"}
                        >
                          ACCOUNT SETTING
                        </Link>
                      </li>
                      <li
                        style={{
                          borderBottom: "1px solid black",
                        }}
                        className="py-2"
                      >
                        <Link
                          className="text-dark whitespace-nowrap text-decoration-none px-2 text-nowrap"
                          to={"/user-membership"}
                        >
                          MY MEMBERSHIP
                        </Link>
                      </li>
                      <li
                        style={{
                          borderBottom: "1px solid black",
                        }}
                        className="py-2"
                      >
                        <Link
                          className="text-dark whitespace-nowrap text-decoration-none px-2 text-nowrap"
                          to={"/purchase-history"}
                        >
                          PURCHASE HISTORY
                        </Link>
                      </li>
                      <li>
                        <button
                          className="btn font-normal"
                          onClick={handleLogout}
                        >
                          LOGOUT
                        </button>
                      </li>
                    </ul>
                  </div>
                ) : (
                  <button
                    type="button"
                    className="btn bg-secondary"
                    onClick={handleLogin}
                  >
                    LOGIN
                  </button>
                )}
              </div>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Navbar;
