import React, { useEffect, useRef, useState } from "react";

const ImageGallery = ({ images }) => {
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";
  const [selectedImage, setSelectedImage] = useState(null);
  const [zoomModalOpen, setzoomModalOpen] = useState(false);

  const handleImageClick = (image) => {
    setSelectedImage(image);
  };

  useEffect(() => {
    setSelectedImage(images[0]);
  }, [images]);

  return (
    <>
      <div style={{ overflow: "hidden" }} className="mb-2">
        <div style={{ height: "90vw", maxHeight: "500px", width: "100%" }}>
          <img
            className="w-100 h-100 object-fit-contain"
            src={baseUrl + selectedImage}
          />
        </div>
      </div>
      <div className="thumbnail-images-container py-1">
        <div className="thumbnail-images w-100">
          {images.map((image, index) => (
            <img
              className="rounded object-fit-contain"
              key={index}
              src={baseUrl + image}
              alt={`Image ${index}`}
              onClick={() => handleImageClick(image)}
            />
          ))}
        </div>
      </div>
    </>
  );
};

export default ImageGallery;
