import React, { useContext, useEffect, useState } from "react";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { getEventById } from "../api/event";
import Moment from "react-moment";
import PlusIcon from "../assets/Images/plus.svg";
import MinusIcon from "../assets/Images/minus.svg";
import { bookEvent } from "../api/booking";
import { checkPaymentStatus, createBookingOrder } from "../api/payment";
import { useStripe } from "@stripe/react-stripe-js";
import moment from "moment";
import { AuthContext } from "../contexts/userContext";

export default function EventBook() {
  const navigate = useNavigate();
  const location = useLocation();
  const stripe = useStripe();
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";
  const params = useParams();

  const [event, setevent] = useState(null);
  const [quantity, setquantity] = useState(1);
  const [name, setname] = useState("");
  const [email, setemail] = useState("");
  const [phone, setphone] = useState("");

  const [message, setmessage] = useState("");

  const incrementQuantity = () => {
    setquantity(quantity + 1);
  };
  const decrementQuantity = () => {
    quantity > 1 && setquantity(quantity - 1);
  };

  const handleGetEvent = () => {
    getEventById({ id: params.eventId })
      .then((res) => {
        setevent(res);
      })
      .catch((err) => {
        console.log(err);
      });
  };

  const handleBookEvent = () => {
    if (name && email && phone) {
      sessionStorage.setItem(
        "bookingData",
        JSON.stringify({
          id: event._id,
          data: {
            name,
            email,
            phone,
            persons: quantity,
          },
        })
      );
      createBookingOrder({
        data: {
          event,
          name,
          email,
          phone,
          persons: quantity,
          total: event.price,
        },
      })
        .then((res) => {
          stripe
            .redirectToCheckout({ sessionId: res.id })
            .then((res) => {})
            .catch((err) => {});
        })
        .catch((err) => {});
    }
  };

  const handleGetData = async () => {
    try {
      const data = JSON.parse(sessionStorage.getItem("bookingData"));

      const stripeSessionId = location.search.split("session_id=")[1];

      if (stripeSessionId && data) {
        checkPaymentStatus({ sessionId: stripeSessionId })
          .then((paymentIntent) => {
            if (paymentIntent.paymentStatus == "succeeded") {
              bookEvent(data)
                .then((res) => {
                  setname("");
                  setemail("");
                  setphone("");
                  setquantity(1);
                  setmessage("EVENT BOOKED SUCCESSFULLY");
                  sessionStorage.setItem("bookingData", null);
                  window.scrollTo(0, 0);
                  setTimeout(() => {
                    setmessage("");
                  }, 8000);
                })
                .catch((err) => {});
            }
          })
          .catch((error) => {
            console.error("Error:", error);
          });
      }
    } catch (err) {}
  };

  const { currentUser } = useContext(AuthContext);

  useEffect(() => {
    if (currentUser && currentUser.plan.planID) {
    } else {
      navigate("/events");
    }
    handleGetEvent();
    handleGetData();
  }, []);

  return (
    <div className="container">
      <h2 className="text-center text-secondary mb-5">BOOK NOW</h2>
      <div>
        <p className="mb-4 text-center h4 text-secondary">{message}</p>
        {event && (
          <div className="row text-white border">
            <div
              className="col-12 col-md-5 position-relative  rounded-2"
              style={{
                background: `url(${baseUrl + event.image})`,
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
                backgroundSize: "cover",
                minHeight: "200px",
                height: "40vw",
                maxHeight: "500px",
              }}
            >
              <div
                className="position-absolute h-100 w-100  "
                style={{ left: 0, background: "rgba(0,0,0,0.5)", zIndex: 1 }}
              ></div>
              <div
                className="d-flex align-items-center justify-content-center h-100 position-relative"
                style={{ zIndex: 2 }}
              >
                <div className="h-75 d-flex align-items-center flex-column justify-content-between text-center">
                  <div>
                    <h2 className="mb-2 ">{event.title}</h2>
                  </div>
                  <h1 className="text-secondary">${event.price}</h1>
                </div>
              </div>
            </div>
            <div className="col-12 col-md-7">
              <div className="w-75 mt-5 mt-md-4 px-2 px-md-4">
                <div>
                  <div className="mb-4">
                    <p className="h4">{event.description}</p>
                    <p className="h5 text-secondary">
                      {moment(event.date).format("DD MMMM, YYYY")}
                      {/* <Moment format="DD MMMM, YYYY">{event.date}</Moment> */}
                    </p>
                  </div>
                  <div className="d-flex flex-column gap-3 font-mont">
                    <div>
                      <label>NAME:</label>
                      <input
                        value={name}
                        onChange={(e) => setname(e.target.value)}
                        type="text"
                        className="form-control bg-transparent rounded-0 text-white"
                      />
                    </div>
                    <div>
                      <label>EMAIL:</label>
                      <input
                        value={email}
                        onChange={(e) => setemail(e.target.value)}
                        type="text"
                        className="form-control bg-transparent rounded-0 text-white"
                      />
                    </div>
                    <div>
                      <label>PHONE:</label>
                      <input
                        value={phone}
                        onChange={(e) => setphone(e.target.value)}
                        type="text"
                        className="form-control bg-transparent rounded-0 text-white"
                      />
                    </div>
                    <div>
                      {/* <div
                        className="d-flex flex-column justify-content-center align-items-center"
                        style={{ width: "40%" }}
                      >
                        <p className="text-light mt-3 mb-1 small fw-bold">
                          PERSON
                        </p>
                        <div className="d-flex gap-2 align-items-center">
                          <div
                            style={{ cursor: "pointer" }}
                            className="rounded-circle border-muted border position-relative d-flex flex-column align-items-center justify-content-center mx-2"
                            onClick={decrementQuantity}
                          >
                            <img
                              src={MinusIcon}
                              className="position-absolute p-1 rounded-circle border border-muted"
                            />
                          </div>
                          <input
                            type="number"
                            className="form-control bg-transparent rounded-0 text-white text-center p-0 m-0 py-1 form-control-sm text-white"
                            value={quantity}
                            readOnly
                          />
                          <div
                            style={{ cursor: "pointer" }}
                            className="rounded-circle border-muted border position-relative d-flex flex-column align-items-center justify-content-center mx-2"
                            onClick={incrementQuantity}
                          >
                            <img
                              src={PlusIcon}
                              className="position-absolute p-1 rounded-circle border border-muted"
                            />
                          </div>
                        </div>
                      </div> */}

                      <div className="d-flex mt-3">
                        {event.totalBookings > 0 ? (
                          <button
                            className="btn bg-white text-dark mt-4 fw-bold rounded-1 px-5"
                            onClick={handleBookEvent}
                          >
                            BOOK NOW
                          </button>
                        ) : (
                          <button
                            className="btn bg-white text-dark mt-4 fw-bold rounded-1 px-5"
                            disabled
                          >
                            SOLD OUT
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mb-5">
              <div className="col-12 col-md-5"></div>
              <div
                className="mt-5 pt-3 col-12 col-md-7"
                style={{ paddingLeft: "4%" }}
              >
                <p className="text-secondary h5">INCLUDES</p>
                {event.features.map((feature) => (
                  <div className="d-flex align-items-center gap-2 mb-2">
                    <p
                      className=" h5 fw-bold m-0 rounded-circle bg-white"
                      style={{ padding: "3px" }}
                    ></p>
                    <p className="h5 m-0 p-0 font-mont">{feature}</p>
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  );
}
