import React, { useState } from "react";
import Moment from "react-moment";
import BookingDetailsModal from "../modals/bookingDetails/BookingDetailModal";
import moment from "moment";

export default function Details({ index, booking }) {
  const [detailsModalOpen, setdetailsModalOpen] = useState(false);

  return (
    <>
      <BookingDetailsModal
        open={detailsModalOpen}
        booking={booking}
        oncancel={() => setdetailsModalOpen(false)}
      />
      <tr className="text-center">
        <td className="pt-3">
          <p className="text-nowrap">{index + 1}</p>
        </td>
        <td className="pt-3">
          <p className="text-nowrap">{booking.eventID.title}</p>
        </td>
        <td className="pt-3">
          <p className="text-nowrap">
            {moment(booking.createdAt).format("DD MMMM, YYYY")}
            {/* <Moment format="DD MMMM, YYYY">{booking.createdAt}</Moment> */}
          </p>
        </td>
        <td className="pt-3">
          <p className="text-nowrap">{booking.name}</p>
        </td>
        <td>
          <button
            className="text-dark text-decoration-none bg-secondary btn fw-bold text-nowrap"
            onClick={() => setdetailsModalOpen(true)}
          >
            View Details
          </button>
        </td>
      </tr>
    </>
  );
}
