import React, { useEffect, useState } from "react";
import { getAllOrders } from "../api/order";
import Moment from "react-moment";
import { Link } from "react-router-dom";
import moment from "moment";

export default function Orders() {
  const [orders, setorders] = useState([]);

  const handleGetOrders = () => {
    getAllOrders()
      .then((res) => {
        setorders(res);
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetOrders();
  }, []);

  return (
    <div>
      {orders.length > 0 && (
        <div className="container font-mont">
          <div class="table-responsive">
            <table class="table table-dark">
              <thead>
                <tr className="text-center">
                  <th scope="col text-nowrap">#</th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Order ID</p>
                  </th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Total Bill</p>
                  </th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Order Placed On</p>
                  </th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Actions</p>
                  </th>
                </tr>
              </thead>
              <tbody>
                {orders.map((order, index) => (
                  <tr className="text-center">
                    <td className="pt-3">
                      <p className="text-nowrap">{index + 1}</p>
                    </td>
                    <td className="pt-3">
                      <p className="text-nowrap">{order._id}</p>
                    </td>
                    <td className="pt-3">
                      <p className="text-nowrap">USD ${order.total}</p>
                    </td>
                    <td className="pt-3">
                      <p className="text-nowrap">
                        {moment(order.createdAt).format("DD MMMM, YYYY")}
                        {/* <Moment format="DD MMMM, YYYY">
                          {order.createdAt}
                        </Moment> */}
                      </p>
                    </td>
                    <td>
                      <Link
                        to={`/order-preview/${order._id}`}
                        className="text-dark text-decoration-none bg-secondary btn fw-bold text-nowrap"
                      >
                        View Details
                      </Link>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
}
