import React, { useContext, useState } from "react";
import "../../assets/css/Event_Card.css";
import PlayIcon from "../../assets/Images/play-icon.svg";
import { deleteSocialPost, updateSocialPost } from "../../api/socialPost";
import { AuthContext } from "../../contexts/userContext";
import ConfirmModal from "../modals/ConfirmModal";
import SocialPostModal from "../modals/socialPost/SocialPostModal";
import ReactPlayer from "react-player";

function HomeYoutubeCard({ youtube }) {
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const { currentUser } = useContext(AuthContext);

  const [editModalOpen, seteditModalOpen] = useState(false);
  const [confirmModalOpen, setconfirmModalOpen] = useState(false);

  const handleUpdate = ({ data }) => {
    updateSocialPost({ data, id: youtube._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {});
  };

  const handleDelete = () => {
    deleteSocialPost({ id: youtube._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {});
  };

  return (
    <div className="w-100 m-auto">
      <SocialPostModal
        open={editModalOpen}
        onconfirm={handleUpdate}
        oncancel={() => seteditModalOpen(false)}
        post={youtube}
      />
      <ConfirmModal
        open={confirmModalOpen}
        oncancel={() => setconfirmModalOpen(false)}
        title={"CONFIRM DELETE"}
        description={"Are you sure you want to delete this post?"}
        onconfirm={handleDelete}
      />

      <div className="mb-5 mb-sm-0  d-flex justify-content-center position-relative bg-white">
        <ReactPlayer
          // controls={false}
          url={youtube.url}
          className="react-player"
          // config={{
          // youtube: {
          // embedOptions: {
          //   controls: 1,
          // },
          // playerVars: { showinfo: 1 },
          // },
          // }}
        />
        {/* <div
          className="rounded home-event-card"
          style={{
            background: `url(${youtube.image})`,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            backgroundSize: "cover",
          }}
        >
          <div
            className="position-absolute h-100 w-100  "
            style={{ left: 0, background: "rgba(0,0,0,0.5)", zIndex: 1 }}
          ></div>
          <div
            className="d-flex align-items-center justify-content-center h-100 position-relative"
            style={{ zIndex: 2 }}
          >
            <div className="h-50 d-flex align-items-center flex-column justify-content-center text-center position-relative">
              <img src={PlayIcon} style={{ height: "60%", width: "60%" }} />
            </div>
          </div>
        </div> */}
      </div>

      {currentUser?.isAdmin && (
        <div className="d-flex gap-3 flex-wrap justify-content-center mt-3">
          <div className="d-flex justify-content-center justify-content-md-start">
            <button
              className="btn bg-white text-dark"
              onClick={() => seteditModalOpen(true)}
            >
              EDIT
            </button>
          </div>
          <div className="d-flex justify-content-center justify-content-md-start">
            <button
              className="btn bg-danger text-white"
              onClick={() => setconfirmModalOpen(true)}
            >
              DELETE
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default HomeYoutubeCard;
