import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { getBookingsOfEvent } from "../api/booking";
import Moment from "react-moment";
import Details from "../components/bookingDetails/Details";

export default function EventBookingsDetails() {
  const { eventId } = useParams();

  const [bookings, setbookings] = useState([]);

  const handleGetBookings = () => {
    getBookingsOfEvent({ id: eventId })
      .then((res) => {
        setbookings(res);
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetBookings();
  }, []);

  return (
    <div>
      {bookings.length > 0 && (
        <div className="container font-mont">
          <div class="table-responsive">
            <table class="table table-dark">
              <thead>
                <tr className="text-center">
                  <th scope="col text-nowrap">#</th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Event Name</p>
                  </th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Event Date</p>
                  </th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Booked By</p>
                  </th>
                  <th scope="col text-nowrap">
                    <p className="text-nowrap">Actions</p>
                  </th>
                </tr>
              </thead>
              <tbody>
                {bookings.map((booking, index) => (
                  <Details booking={booking} index={index} />
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}
    </div>
  );
}
