import React, { useEffect, useState } from "react";
import { getPaymentHistory } from "../api/payment";
import Moment from "react-moment";
import { Link } from "react-router-dom";
import BookingDetailsModal from "../components/modals/bookingDetails/BookingDetailModal";
import moment from "moment";

export default function PurchaseHistory() {
  const [payments, setpayments] = useState([]);
  const [detailsModalOpen, setdetailsModalOpen] = useState(false);
  const [activeDetails, setactiveDetails] = useState(null);

  const handleGetPayments = () => {
    getPaymentHistory()
      .then((res) => {
        setpayments(res);
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetPayments();
  }, []);

  return (
    <div className="container">
      <BookingDetailsModal
        open={detailsModalOpen}
        booking={activeDetails?.bookingID}
        oncancel={() => setdetailsModalOpen(false)}
      />
      <div className="m-auto w-75">
        <p className="text-secondary text-center fw-bold h2">
          PURCHASE HISTORY
        </p>
        <p className="text-white mt-5 m-auto text-center">
          View your purchase history to keep track of your transactions related
          to your membership and merchandise:
        </p>
        <div class="table-responsive font-mont">
          <table class="table table-dark">
            <thead>
              <tr className="text-center">
                <th scope="col text-nowrap">DATE</th>
                <th scope="col text-nowrap">
                  <p className="text-nowrap">AMOUNT</p>
                </th>
                <th scope="col text-nowrap">
                  <p className="text-nowrap">VIEW DETAILS</p>
                </th>
              </tr>
            </thead>
            <tbody>
              {payments.map((payment, index) => (
                <tr className="text-center">
                  <td className="pt-3">
                    {moment(payment.orderID?.createdAt).format("DD MMMM, YYYY")}
                    {/* <Moment format="MM DD, YYYY">
                      {payment.orderID?.createdAt}
                    </Moment> */}
                  </td>
                  <td className="pt-3">
                    {payment.orderID ? (
                      <p className="text-nowrap">
                        USD ${payment.orderID.total}
                      </p>
                    ) : (
                      <p className="text-nowrap">
                        USD ${payment.bookingID?.eventID?.price}
                      </p>
                    )}
                  </td>
                  <td>
                    {payment.orderID ? (
                      <Link
                        to={`/order-preview/${payment.orderID?._id}`}
                        className="text-dark text-decoration-none bg-secondary btn fw-bold text-nowrap"
                      >
                        View Details
                      </Link>
                    ) : (
                      <button
                        className="text-dark text-decoration-none bg-secondary btn fw-bold text-nowrap"
                        onClick={() => {
                          setactiveDetails(payment);
                          setdetailsModalOpen(true);
                        }}
                      >
                        View Details
                      </button>
                    )}
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}
