import React, { useContext, useEffect } from "react";
import "../assets/css/checkout.css";
import { Link, useNavigate } from "react-router-dom";
import { AuthContext } from "../contexts/userContext";
import CheckoutLeft from "../components/checkout/CheckoutLeft";
import CheckoutRight from "../components/checkout/CheckoutRight";

export default function Checkout() {
  const navigate = useNavigate();
  const { cart, setcart } = useContext(AuthContext);

  useEffect(() => {
    if (!cart.products.length > 0) navigate("/merch");
  }, []);

  return (
    <div>
      <div className="text-center text-secondary">
        <h2>
          <u>CHECK OUT</u>
        </h2>
      </div>
      <div className="container mt-5 pt-5 font-mont">
        <div className="row">
          <div className="checkout-left-side px-lg-5 col-lg-7 col-xl-7 order-2 order-lg-1 mt-5 mt-lg-0 ">
            <CheckoutLeft cart={cart} setcart={setcart} />
          </div>
          <div className="order-1 order-lg-2 checkout-right-side col-lg-5 col-xl-5 text-white px-0 px-lg-4 py-5 py-lg-0 mt-4">
            <CheckoutRight />
          </div>
        </div>
      </div>
    </div>
  );
}
