import React, { useContext } from "react";
import "../../assets/css/Product_Card.css";
import "../../assets/css/MembershipCard.css";
import { useState } from "react";
import MembershipModal from "../modals/membership/MembershipModal";
import ConfrimMdal from "../modals/ConfirmModal";
import Moment from "react-moment";
import { deleteEvent, updateEvent } from "../../api/event";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../contexts/userContext";
import moment from "moment";

function EventCard({ event }) {
  const { currentUser } = useContext(AuthContext);

  const navigate = useNavigate();

  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const [editModalOpen, seteditModalOpen] = useState(false);
  const [confirmModalOpen, setconfirmModalOpen] = useState(false);

  const handleEditEvent = ({ data }) => {
    updateEvent({ data, id: event._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        seteditModalOpen(false);
      });
  };
  const handleDeleteEvent = () => {
    deleteEvent({ id: event._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        setconfirmModalOpen(false);
      });
  };

  const formatDate = (date) => {
    const localDate = new Date(date); // JavaScript will use the user's time zone

    // Format the local date for display
    const options = {
      year: "numeric",
      month: "long",
      day: "numeric",
    };
    const formattedDate = localDate.toLocaleDateString(undefined, options);

    return formattedDate;
  };

  return (
    <div className="border rounded overflow-hidden">
      <MembershipModal
        type={"Event"}
        open={editModalOpen}
        onconfirm={handleEditEvent}
        oncancel={() => seteditModalOpen(false)}
        data={event}
      />
      <ConfrimMdal
        title={"CONFIRM DELETE"}
        description={"Are you sure you want to delete this event?"}
        open={confirmModalOpen}
        onconfirm={handleDeleteEvent}
        oncancel={() => setconfirmModalOpen(false)}
      />
      <div className="row text-white">
        <div
          className="col-12 col-md-5 position-relative membership-left"
          style={{
            background: `url(${baseUrl + event.image})`,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            backgroundSize: "cover",
          }}
        >
          <div
            className="position-absolute h-100 w-100 "
            style={{ left: 0, background: "rgba(0,0,0,0.5)", zIndex: 1 }}
          ></div>
          <div
            className="d-flex align-items-center justify-content-center h-100 position-relative"
            style={{ zIndex: 2 }}
          >
            <div className="h-75 d-flex align-items-center flex-column justify-content-between text-center">
              <div>
                <h2 className="mb-2 ">{event.title}</h2>
              </div>
              <h1 className="text-secondary">${event.price}</h1>
            </div>
          </div>
        </div>
        <div className="col-12 col-md-6">
          <div className="py-4 px-3 p-md-4 d-flex flex-column justify-content-between h-100">
            <div>
              <h4 className="mb-2">{event.description}</h4>
              <h4 className="mb-2">
                DATE : {moment(event.date).format("DD MMMM, YYYY")}
              </h4>
              <h5 className="mt-4 mb-3 text-secondary">INCLUDES:</h5>
              {event.features.map((feature) => (
                <div className="d-flex align-items-center gap-2 mb-2">
                  <p
                    className=" h5 fw-bold m-0 rounded-circle bg-white"
                    style={{ padding: "3px" }}
                  ></p>
                  <p className="h5 m-0 p-0 font-mont">{feature}</p>
                </div>
              ))}
            </div>
            <div>
              <div className="d-flex justify-content-center justify-content-md-start">
                {event.totalBookings > 0 && currentUser?.plan.planID ? (
                  <button
                    className="btn bg-white text-dark mt-4 px-5"
                    onClick={() => navigate(`/event-book/${event._id}`)}
                  >
                    BOOK NOW
                  </button>
                ) : !(event.totalBookings > 0) ? (
                  <button
                    className="btn bg-white text-dark mt-4 fw-bold rounded-1 px-5"
                    disabled
                  >
                    SOLD OUT
                  </button>
                ) : (
                  <button
                    className="btn bg-white text-dark mt-4 fw-bold rounded-1 px-5"
                    disabled
                  >
                    ONLY FOR MEMEBERS
                  </button>
                )}
              </div>
              {currentUser?.isAdmin && (
                <div className="d-flex gap-3 flex-wrap justify-content-center justify-content-md-start mt-3">
                  <div className="d-flex justify-content-center justify-content-md-start">
                    <button
                      className="btn bg-white text-dark"
                      onClick={() => seteditModalOpen(true)}
                    >
                      EDIT
                    </button>
                  </div>
                  <div className="d-flex justify-content-center justify-content-md-start">
                    <button
                      className="btn bg-danger text-white"
                      onClick={() => setconfirmModalOpen(true)}
                    >
                      DELETE
                    </button>
                  </div>
                  <div className="d-flex justify-content-center justify-content-md-start">
                    <button
                      className="btn bg-secondary text-white"
                      onClick={() => navigate(`/bookings/${event._id}`)}
                    >
                      View Bookings
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default EventCard;
