import React, { useContext, useEffect, useState } from "react";
import HomeMerch from "../components/Home/HomeMerch";
import AddProductModal from "../components/modals/Product/AddProductModal";
import { addProduct, getProducs } from "../api/product";
import Pagination from "../components/Pagination";
import { AuthContext } from "../contexts/userContext";

export default function Merch() {
  const limit = 8;

  const { currentUser } = useContext(AuthContext);

  const [skip, setskip] = useState(0);
  const [hasMoreProducts, sethasMoreProducts] = useState(false);
  const [productModalOpen, setproductModalOpen] = useState(false);
  const [loadingProducts, setloadingProducts] = useState(true);

  const [merch, setmerch] = useState([]);

  const handleAddProduct = ({ data, files }) => {
    data["type"] = "merch";
    addProduct({ data, files })
      .then((res) => {
        setproductModalOpen(false);
        window.location.reload();
      })
      .catch((err) => {
        setproductModalOpen(false);
      });
  };

  const handleGetProducts = () => {
    setloadingProducts(true);
    getProducs({ limit: limit + 1, skip, type: "merch" })
      .then((res) => {
        if (res.length > limit) {
          sethasMoreProducts(true);
          res.pop();
        } else {
          sethasMoreProducts(false);
        }
        setmerch(res);
        setloadingProducts(false);
      })
      .catch((err) => {
        setloadingProducts(false);
      });
  };

  const handleGetNext = () => {
    setskip(skip + limit);
  };
  const handleGetPrev = () => {
    setskip(skip - limit);
  };

  useEffect(() => {
    handleGetProducts();
  }, [skip]);

  return (
    <div>
      <div className="merch-title mb-5 pb-5">
        <p className="text-white mb-2 small">FISHING THE ATLANTIC MERCH</p>
        <p className=" text-secondary h2">MERCH</p>
      </div>

      <div>
        <AddProductModal
          open={productModalOpen}
          onconfirm={handleAddProduct}
          oncancel={() => setproductModalOpen(false)}
          type={"product"}
        />
        {currentUser?.isAdmin && (
          <div className="text-end mb-5">
            <button
              className="btn bg-white btn-lg"
              onClick={() => setproductModalOpen(true)}
            >
              Add Product
            </button>
          </div>
        )}
        {loadingProducts ? (
          ""
        ) : (
          <>
            {merch.length > 0 && (
              <HomeMerch merch={merch} setmerch={setmerch} />
            )}
          </>
        )}
        <Pagination
          isloading={loadingProducts}
          skip={skip}
          hasMoreProducts={hasMoreProducts}
          handleGetNext={handleGetNext}
          handleGetPrev={handleGetPrev}
        />
      </div>
    </div>
  );
}
