import React, { useContext, useEffect, useState } from "react";
import demo from "../assets/Images/demo-merch-img.png";
import PlusIcon from "../assets/Images/plus.svg";
import MinusIcon from "../assets/Images/minus.svg";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { getProducs, getProduct } from "../api/product";
import { AuthContext } from "../contexts/userContext";
import { updateCart } from "../api/cart";
import HomeMerch from "../components/Home/HomeMerch";
import ImageGallery from "../components/ImageGallery";

export default function ProductDetails() {
  const { productId } = useParams();
  const location = useLocation();

  const navigate = useNavigate();

  const { cart, setcart, setcartOpen, currentUser } = useContext(AuthContext);

  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const [product, setproduct] = useState(null);
  const [loading, setloading] = useState(false);
  const [quantity, setquantity] = useState(1);
  const [size, setsize] = useState("");
  const [products, setproducts] = useState([]);
  const [proType, setproType] = useState(null);

  const handleGetMerch = () => {
    getProducs({ limit: 4, skip: 0, type: "merch" })
      .then((res) => {
        setproducts(res);
      })
      .catch((err) => {});
  };

  const handleGetProducts = () => {
    getProducs({ limit: 4, skip: 0, type: "product" })
      .then((res) => {
        setproducts(res);
      })
      .catch((err) => {});
  };

  const incrementQuantity = () => {
    quantity < product?.quantity && setquantity(quantity + 1);
  };
  const decrementQuantity = () => {
    quantity > 1 && setquantity(quantity - 1);
  };

  const handleGetProduct = async () => {
    setloading(true);
    getProduct({ id: productId })
      .then((res) => {
        setproType(res.type);
        setsize(res.sizes.length > 0 ? res.sizes[0] : setsize(""));
        setproduct(res);
        setloading(false);
      })
      .catch((err) => {
        setloading(false);
      });
  };

  const handleAddToCart = () => {
    let cartCopy = { ...cart };

    const a = cartCopy.products.filter((pro) => {
      return pro.product._id == product._id;
    });

    let totalProducts = 0;

    a.map((pro) => (totalProducts += pro.quantity));

    if (
      totalProducts < product.quantity &&
      totalProducts + quantity <= product.quantity
    ) {
      const proFound = cartCopy.products.find((pro) => {
        return pro.product._id == product._id && pro.size == size;
      });
      if (proFound) {
        cartCopy = {
          ...cartCopy,
          products: cartCopy.products.map((pro) => {
            if (
              pro.product._id == proFound.product._id &&
              pro.size == proFound.size
            ) {
              return {
                ...pro,
                quantity: pro.quantity + quantity,
              };
            }
            return pro;
          }),
          total: cartCopy.total + quantity * product.price,
        };
      } else {
        cartCopy = {
          ...cartCopy,
          products: [...cartCopy.products, { product, size, quantity }],
          total: cartCopy.total + quantity * product.price,
        };
      }
    }

    const finalProducts = cartCopy.products.map((pro) => {
      return {
        ...pro,
        product: pro.product._id,
      };
    });

    setcart({ ...cartCopy });

    setcartOpen(true);

    window.scrollTo(0, 0);

    updateCart({ total: cartCopy.total, products: finalProducts })
      .then((res) => {})
      .catch((err) => {});
  };

  const handleNavigation = () => {
    proType != "merch" ? navigate("/products") : navigate("/merch");
  };

  useEffect(() => {
    handleGetProduct();
  }, [location.pathname]);

  useEffect(() => {
    if (proType) {
      proType != "merch" ? handleGetProducts() : handleGetMerch();
    }
  }, [proType]);

  return (
    <div className="mt-5 pt-5">
      {!loading && product ? (
        <div className="container ">
          <div className="row">
            <div className="col-12 col-md-6">
              <div className="p-0 p-lg-3 p-xl-5">
                <ImageGallery images={product.productImages} />
                {/* <img
                  className="rounded-2 h-100"
                  style={{
                    maxHeight: "400px",
                    width: "100%",
                  }}
                  src={baseUrl + product.productImages[0]}
                ></img> */}
              </div>
            </div>
            <div className=" text-light col-12 col-md-6">
              <div className="mt-5 mt-md-3 px-3 px-lg-0 h-75 m-auto d-flex flex-column justify-content-center align-items-md-start w-100">
                <div className="details-right-wrapper">
                  <h1 className="font-mont">{product.name}</h1>

                  <div>
                    <div className="d-flex flex-column justify-content-center align-items-center w-75">
                      <p className="text-light mt-3 mb-1 small">Quantity</p>
                      <div className="d-flex gap-2 align-items-center">
                        <div
                          style={{ cursor: "pointer" }}
                          className="rounded-circle border-muted border position-relative d-flex flex-column align-items-center justify-content-center mx-2"
                          onClick={decrementQuantity}
                        >
                          <img
                            src={MinusIcon}
                            className="position-absolute p-1 rounded-circle border border-muted"
                          />
                        </div>
                        <input
                          type="number"
                          className="form-control bg-transparent rounded-0 text-white text-center p-0 m-0 py-1 px-4 form-control-sm"
                          value={quantity}
                          readOnly
                        />
                        <div
                          style={{ cursor: "pointer" }}
                          className="rounded-circle border-muted border position-relative d-flex flex-column align-items-center justify-content-center mx-2"
                          onClick={incrementQuantity}
                        >
                          <img
                            src={PlusIcon}
                            className="position-absolute p-1 rounded-circle border border-muted"
                          />
                        </div>
                      </div>
                    </div>
                    {product.sizes.length > 0 ? (
                      <div className="mt-4">
                        <select
                          className="form-control-sm bg-transparent text-light font-russo form-select text-dark rounded-0"
                          aria-label="Default select example"
                          onChange={(e) => setsize(e.target.value)}
                        >
                          {product.sizes.map((size) => (
                            <option className="text-dark" value={size}>
                              {size.toUpperCase()}
                            </option>
                          ))}
                        </select>
                      </div>
                    ) : (
                      ""
                    )}
                    <div>
                      <h4 className="text-secondary my-4">
                        $ {product.price.toFixed(2)}
                      </h4>
                      {product.quantity > 0 ? (
                        <button
                          className="btn bg-light w-75 text-dark"
                          onClick={handleAddToCart}
                          // disabled={!currentUser}
                        >
                          ADD TO CART
                        </button>
                      ) : (
                        <button
                          className="btn bg-light w-75 text-dark"
                          disabled
                        >
                          SOLD OUT
                        </button>
                      )}
                    </div>
                    {product.features.length > 0 && (
                      <div className="mt-5">
                        <h5 className="mb-3 mt-4 text-white">FEATURES</h5>
                        {product.features.map((feature) => (
                          <div className="d-flex align-items-center gap-2 mb-2 text-white">
                            <p
                              className=" h5 fw-bold m-0 rounded-circle bg-white"
                              style={{ padding: "3px" }}
                            ></p>
                            <p className="h5 m-0 p-0 font-mont">{feature}</p>
                          </div>
                        ))}
                      </div>
                    )}
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="mt-5 pt-5">
            <div className="text-center mb-5">
              <p className="text-white mb-2 small">FISHING THE ATLANTIC</p>
              <p className=" text-secondary h2">
                MORE {proType != "merch" ? "PRODUCTS" : "MERCH"}
              </p>
            </div>
            <HomeMerch merch={products} />
            <div className="text-center">
              <button
                className="btn bg-white text-dark"
                onClick={handleNavigation}
              >
                VIEW ALL {proType != "merch" ? "PRODUCTS" : "MERCH"}
              </button>
            </div>
          </div>
        </div>
      ) : (
        ""
      )}
    </div>
  );
}
