import React, { useEffect, useState } from "react";
import "react-phone-number-input/style.css";
import paypal from "../../assets/Images/paypal.png";
import { parsePhoneNumber } from "react-phone-number-input";
import { isValidPhoneNumber } from "react-phone-number-input";
import { Country, State, City } from "country-state-city";
import { createOrder } from "../../api/order";
import PhoneInput from "react-phone-number-input";
import { updateCart } from "../../api/cart";

export default function OrderPreviewLeft({ order }) {
  return (
    <div className="px-lg-4">
      <div className="mt-5">
        <p className=" text-light fw-bold h4">Delivery:</p>
        <label className="text-white form-label h5 my-2">Phone:</label>
        <input
          readOnly
          value={order.deliveryAddress.phone}
          className="form-control-lg bg-muted w-100 form-control  text-dark "
        ></input>
        <label className="text-white form-label h5 my-2">Country:</label>
        <select
          name="country"
          className="form-control-lg form-control"
          id="country"
          disabled
        >
          <option>{order.deliveryAddress.country}</option>
        </select>
        <div className="d-flex gap-2 mt-4">
          <div>
            <label className="text-white form-label h5 my-2">First Name:</label>
            <input
              readOnly
              value={order.deliveryAddress.firstName}
              className="form-control-lg bg-muted w-100 form-control  text-dark "
            ></input>
          </div>
          <div>
            <label className="text-white form-label h5 my-2">Last Name:</label>
            <input
              readOnly
              value={order.deliveryAddress.lastName}
              className="form-control-lg bg-muted w-100 form-control  text-dark"
            ></input>
          </div>
        </div>
        <div className="mt-4">
          <label className="text-white form-label h5 my-2">Address:</label>
          <input
            readOnly
            value={order.deliveryAddress.address}
            className="form-control-lg bg-muted w-100 form-control text-dark"
          ></input>
        </div>
        <div className="d-flex gap-2 mt-4 ">
          <div style={{ flex: 1 }}>
            <label className="text-white form-label h5 my-2">City:</label>
            <select
              disabled
              className="form-control-lg bg-muted w-100 form-control  text-dark"
            >
              <option>{order.deliveryAddress.city}</option>
            </select>
          </div>
          <div style={{ flex: 1 }}>
            <label className="text-white form-label h5 my-2">State:</label>
            <select
              disabled
              className="form-control-lg bg-muted w-100 form-control  text-dark"
            >
              <option>{order.deliveryAddress.state}</option>
            </select>
          </div>
          <div style={{ flex: 1 }}>
            <label className="text-white form-label h5 my-2">Post Code</label>
            <input
              readOnly
              value={order.deliveryAddress.postCode}
              className=" form-control-lg bg-muted w-100 form-control  text-dark"
            ></input>
          </div>
        </div>
      </div>
    </div>
  );
}
