import React from "react";
import about_image from "../assets/Images/about_image.png";
import "../assets/css/About.css";
import team1 from "../assets/Images/team1.png";
import team2 from "../assets/Images/team2.png";
import team3 from "../assets/Images/team3.png";
import "../assets/css/About.css";

export default function About() {
  return (
    <div className="container">
      <div className="text-center">
        <h3 className="h2 about-title1 mb-5 pb-5">ABOUT CLUB</h3>
        <p className="h4 about-title2 my-5">WELCOME TO FISHING THE ATLANTIC</p>
        <img className="about-image" src={about_image}></img>
      </div>

      <p className="h5 fw-normal line-height-md text-white text-center w-75 m-auto my-5 font-mont">
        At Fishing the Atlantic (FTA), our mission is simple: to provide
        passionate anglers with premium products, unforgettable experiences, and
        a strong connection to the sea. We are committed to preserving the
        beauty of the Atlantic coast, With a curated selection of high-quality
        equipment and a dedication to excellence, we strive to enhance your
        fishing journey and foster a deep appreciation for the ocean.
      </p>
      <div className="mt-5 pt-5">
        <p className="title-contact h2 text-center mb-5">OUR TEAM</p>

        <div className="pb-5">
          <div class="container">
            <div class="row m-auto gap-xl-5">
              <div className="col-12 col-md-4 col-xl-3 mb-5 mx-auto">
                <div className="card border-0">
                  <img
                    className="card-img-top"
                    src={team1}
                    alt="Card image cap"
                  />
                  <div
                    className="card-body  text-center card-lower-body"
                    style={{ height: "100px" }}
                  >
                    <p className="card-text team-name h4 font-mont fw-bold">
                      Kalvin Chung
                    </p>
                    <p className="team-title font-mont fw-normal">Founder</p>
                  </div>
                </div>
              </div>

              <div className="col-12 col-md-4 col-xl-3 mb-5 mx-auto">
                <div className="card border-0">
                  <img
                    className="card-img-top"
                    src={team2}
                    alt="Card image cap"
                  />
                  <div
                    className="card-body  text-center card-lower-body"
                    style={{ height: "100px" }}
                  >
                    <p className="card-text team-name h4 font-mont fw-bold">
                      Chris Song
                    </p>
                    <p className="team-title font-mont fw-normal">
                      Cheif marketing officer
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-12 col-md-4 col-xl-3 mb-5 mx-auto">
                <div className="card border-0">
                  <img
                    className="card-img-top"
                    src={team3}
                    alt="Card image cap"
                  />
                  <div
                    className="card-body  text-center card-lower-body"
                    style={{ height: "100px" }}
                  >
                    <p className="card-text team-name h4 font-mont fw-bold">
                      Asfand Ansari
                    </p>
                    <p className="team-title font-mont fw-normal">
                      Cheif technology officer
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
