import React from "react";
import "../../assets/css/Merch.css";
import { useState } from "react";
import HomeEventsCard from "./HomeEventsCard";

function HomeEvents({ events }) {
  return (
    <div className="">
      <div className="text-center ">
        <div>
          <div className="d-block d-sm-flex justify-content-center gap-3 flex-wrap flex-md-nowrap">
            {events.map((e, i) => {
              return <HomeEventsCard event={e} />;
            })}
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomeEvents;
