import React, { useState } from "react";
import {
  Button,
  Input,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from "reactstrap";

export default function SocialPostModal({ open, oncancel, onconfirm, post }) {
  const [image, setimage] = useState("");
  const [url, seturl] = useState(post ? post.url : "");
  const [type, settype] = useState(post ? post.type : "instagram");
  const [apiCalled, setapiCalled] = useState(false);

  const handleOnConfirm = () => {
    if (type == "instagram") {
      if ((image || post) && type && url) {
        onconfirm({ data: { url, type, createdAt: Date.now(), image } });
      }

      setapiCalled(true);
    } else {
      if (type && url) {
        onconfirm({ data: { url, type } });
      }

      setapiCalled(true);
    }
  };

  return (
    <div>
      <Modal isOpen={open} centered>
        <ModalHeader>{post ? `Update Post` : `Add Post`}</ModalHeader>
        <ModalBody>
          <div>
            <label className="form-label">Url :</label>
            <input
              className="form-control"
              value={url}
              onChange={(e) => seturl(e.target.value)}
            />
          </div>
          <div className="mt-3">
            {!post && (
              <div>
                <label className="form-label">Type:</label>
                <select
                  className="form-select"
                  value={type}
                  onChange={(e) => settype(e.target.value)}
                >
                  <option value={"instagram"}>Instagram</option>
                  <option value={"youtube"}>Youtube</option>
                </select>
              </div>
            )}
            {type == "instagram" && (
              <div className="mt-3">
                <label className="form-label">Image:</label>

                <Input
                  accept="image/*"
                  type="file"
                  onChange={(e) => setimage(e.target.files[0])}
                />
              </div>
            )}
          </div>
        </ModalBody>
        <ModalFooter>
          <div>
            <button
              className="btn bg-primary text-white"
              onClick={handleOnConfirm}
              disabled={apiCalled}
            >
              {post ? `Update Post` : `Add Post`}
            </button>{" "}
            <Button color="secondary" onClick={oncancel} disabled={apiCalled}>
              Cancel
            </Button>
          </div>
        </ModalFooter>
      </Modal>
    </div>
  );
}
