import React, { useContext, useEffect, useState } from "react";
import "../assets/css/checkout.css";
import { Link, useNavigate, useParams } from "react-router-dom";
import { AuthContext } from "../contexts/userContext";
import OrderPreviewLeft from "../components/orderPreview.js/OrderPreviewLeft";
import OrderPreviewRight from "../components/orderPreview.js/OrderPreviewRight";
import { getOrder } from "../api/order";

export default function OrderPreview() {
  const { orderId } = useParams();

  const navigate = useNavigate();

  const [order, setorder] = useState(null);

  const { currentUser } = useContext(AuthContext);

  const handleGetOrder = () => {
    getOrder({ id: orderId })
      .then((res) => {
        if (currentUser?.isAdmin || currentUser?._id == res.userID) {
          setorder(res);
        } else {
          navigate("/");
        }
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetOrder();
  }, []);

  return (
    <div>
      <div className="text-center text-secondary">
        <h1>
          <u>ORDER PREVIEW</u>
        </h1>
      </div>
      {order && (
        <div className="container mt-5 pt-5 font-mont">
          <p
            className="text-white h4 px-lg-5 text-secondary"
            style={{ wordBreak: "break-all" }}
          >
            Order ID: {order._id}
          </p>
          <div className="row">
            <div className="checkout-left-side px-lg-5 col-lg-7 col-xl-7 order-2 order-lg-1 mt-5 mt-lg-0 ">
              <OrderPreviewLeft order={order} />
            </div>
            <div className="order-1 order-lg-2 checkout-right-side col-lg-5 col-xl-5 text-white px-0 px-lg-4 py-5 py-lg-0 mt-4">
              <OrderPreviewRight order={order} />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
