import { axiosClient } from "./axios";

export const updateBanner = async ({ type, image }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.put(`/banner/${type}`, { image });

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getBanner = async ({ type }) => {
  try {
    const res = await axiosClient.get(`/banner?type=${type}`);
    return res.data;
  } catch (err) {
    throw err;
  }
};
