import React, { useContext, useEffect, useState } from "react";
import "react-phone-number-input/style.css";
import { createOrder } from "../../api/order";
import { updateCart } from "../../api/cart";
import { checkPaymentStatus, createPayment } from "../../api/payment";
import { useStripe } from "@stripe/react-stripe-js";
import { useLocation } from "react-router-dom";
import UserDeliveryDetails from "../UserDeliveryDetails";
import { AuthContext } from "../../contexts/userContext";

export default function CheckoutLeft() {
  const { cart, setcart, currentUser } = useContext(AuthContext);

  const location = useLocation();
  const stripe = useStripe();

  const [message, setmessage] = useState("");

  const makePayment = () => {
    createPayment({ data: cart })
      .then((res) => {
        stripe
          .redirectToCheckout({ sessionId: res.id })
          .then((res) => {})
          .catch((err) => {});
      })
      .catch((err) => {});
  };

  const handleGetData = async () => {
    try {
      const data = JSON.parse(sessionStorage.getItem("orderData"));

      const stripeSessionId = location.search.split("session_id=")[1];

      if (stripeSessionId && data) {
        checkPaymentStatus({ sessionId: stripeSessionId })
          .then((paymentIntent) => {
            if (paymentIntent.paymentStatus == "succeeded") {
              createOrder({
                products: data.products,
                deliveryAddress: data.deliveryAddress,
                total: data.total,
              })
                .then((res) => {
                  window.scrollTo(0, 400);
                  setmessage(
                    "Order Place successfully. Your order number is: " + res._id
                  );
                  updateCart({ products: [], total: 0 })
                    .then((res) => {})
                    .catch((err) => {});
                  setcart({ products: [], total: 0 });
                  sessionStorage.setItem("orderData", null);
                })
                .catch((err) => {
                  window.scrollTo(0, 400);
                  setmessage(err.response.data.message);
                });
            } else {
            }
          })
          .catch((error) => {
            console.error("Error:", error);
          });
      }
    } catch (err) {}
  };

  useEffect(() => {
    handleGetData();
  }, []);

  return (
    <div className="px-lg-4">
      <p className="text-secondary text-center fw-bold h4">{message}</p>
      <UserDeliveryDetails
        onsubmit={makePayment}
        SubmitBtn={SubmitBtn}
        message={message}
        setmessage={setmessage}
      />
    </div>
  );
}

const SubmitBtn = ({ clickHandler }) => {
  return (
    <div className="d-flex flex-column align-items-center justify-content-center">
      <button
        className="mb-5 btn w-100 mt-4 bg-muted btn-lg  text-dark fw-bold"
        onClick={clickHandler}
      >
        Pay Now
      </button>
    </div>
  );
};
