import React, { useContext, useEffect, useState } from "react";
import { parsePhoneNumber } from "react-phone-number-input";
import { isValidPhoneNumber } from "react-phone-number-input";
import { Country, State, City } from "country-state-city";
import PhoneInput from "react-phone-number-input";
import { AuthContext } from "../contexts/userContext";
import { useNavigate } from "react-router-dom";

export default function UserDeliveryDetails({
  onsubmit,
  SubmitBtn,
  setmessage,
}) {
  const { currentUser } = useContext(AuthContext);

  const [value, setValue] = useState(
    currentUser ? currentUser.deliveryAddress?.phone : ""
  );
  const [isValid, setIsValid] = useState(null);
  const [formData, setFormData] = useState({
    city: currentUser ? currentUser.deliveryAddress?.city : null,
    postCode: currentUser ? currentUser.deliveryAddress?.postCode : null,
    address: currentUser ? currentUser.deliveryAddress?.address : null,
    country: currentUser ? currentUser.deliveryAddress?.country : null,
    state: currentUser ? currentUser.deliveryAddress?.state : null,
    firstName: currentUser ? currentUser.deliveryAddress?.firstName : null,
    lastName: currentUser ? currentUser.deliveryAddress?.lastName : null,
  });

  const { cart } = useContext(AuthContext);

  const handleSubmit = () => {
    let flag = true;
    for (const [key, value] of Object.entries(formData)) {
      if (value == null) {
        flag = false;
      }
    }
    if (!flag || !isValid) {
      window.scrollTo(0, 400);
      setmessage("Please Fill All Fields");
      setTimeout(() => {
        setmessage("");
      }, 3000);
    } else {
      const finalProducts = cart.products.map((product) => {
        return {
          ...product,
          product: product.product._id,
          price: product.product.price,
        };
      });

      formData["phone"] = value;

      sessionStorage.setItem(
        "orderData",
        JSON.stringify({
          products: finalProducts,
          deliveryAddress: formData,
          total: !currentUser?.plan.planID ? cart.total : cart.total - 10,
        })
      );
      onsubmit({ data: formData });
    }
  };

  const changeHandler = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  useEffect(() => {
    if (value) {
      setIsValid(false);

      if (value.length > 0) {
        if (isValidPhoneNumber(value)) {
          setIsValid(true);

          let countryName = parsePhoneNumber(value);
          if (countryName) {
            let country = Country.getCountryByCode(countryName.country);
            if (country) {
              let state = State.getStatesOfCountry(countryName.country);
              let city = City.getCitiesOfState(
                countryName.country,
                state[0].isoCode
              );
              setFormData({
                ...formData,
                country: country.name,
                state: state[0].isoCode,
                city: city[0].name,
              });
            }
          }
        }
      }
    }
  }, [value]);

  return (
    <div className="mt-5">
      <p className=" text-light fw-bold h4">Delivery</p>
      <PhoneInput
        placeholder="Enter phone number"
        className="phone-input"
        defaultCountry="US"
        value={value}
        onChange={setValue}
      />
      {isValid == false ? (
        <small style={{ color: "red" }}>Enter a valid phone number</small>
      ) : null}

      <select
        name="country"
        className="form-control-lg form-control"
        id="country"
        disabled
        value={formData.country}
        onChange={changeHandler}
      >
        {Country.getAllCountries().map((country, val, index) => {
          return (
            <option key={val} value={country.name}>
              {country.name}
            </option>
          );
        })}
      </select>
      <div className="d-flex gap-2 mt-4">
        <input
          name="firstName"
          value={formData.firstName}
          onChange={changeHandler}
          className="form-control-lg bg-muted w-100 form-control  text-dark "
          placeholder="First name"
        ></input>
        <input
          name="lastName"
          value={formData.lastName}
          onChange={changeHandler}
          className="form-control-lg bg-muted w-100 form-control  text-dark"
          placeholder="Last name"
        ></input>
      </div>
      <input
        name="address"
        value={formData.address}
        onChange={changeHandler}
        className="form-control-lg bg-muted w-100 form-control mt-4  text-dark"
        placeholder="Address"
      ></input>
      <div className="d-flex gap-2 mt-4 ">
        <select
          name="city"
          value={formData.city}
          onChange={changeHandler}
          className="form-control-lg bg-muted w-100 form-control  text-dark"
          id="city"
        >
          {isValid && value ? (
            value.length > 0 && isValidPhoneNumber(value) ? (
              City.getCitiesOfState(
                parsePhoneNumber(value).country,
                formData.state
              ).map((city, index) => {
                return (
                  <option key={index} value={city.name}>
                    {city.name}
                  </option>
                );
              })
            ) : null
          ) : (
            <option value={""}>City</option>
          )}
        </select>
        <select
          name="state"
          onChange={changeHandler}
          className="form-control-lg bg-muted w-100 form-control  text-dark"
          id="city"
        >
          {isValid && value ? (
            value.length > 0 && isValidPhoneNumber(value) ? (
              State.getStatesOfCountry(parsePhoneNumber(value).country).map(
                (state, index) => {
                  return (
                    <option key={index} value={state.isoCode}>
                      {state.name}
                    </option>
                  );
                }
              )
            ) : null
          ) : (
            <option value={""}>State</option>
          )}
        </select>
        <input
          name="postCode"
          value={formData.postCode}
          onChange={changeHandler}
          className=" form-control-lg bg-muted w-100 form-control  text-dark"
          placeholder="Postcode"
        ></input>
      </div>
      <SubmitBtn clickHandler={handleSubmit} />
    </div>
  );
}
