import React, { useContext, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { checkPaymentStatus } from "../api/payment";
import { updatePlan } from "../api/user";
import { AuthContext } from "../contexts/userContext";

export default function MembershipSuccess() {
  const location = useLocation();
  const navigate = useNavigate();

  const { currentUser, setUser } = useContext(AuthContext);

  const nextSteps = [
    "Explore the Website: Take some time to explore our website and discover all the resources and features available exclusively to members.",
    "Stay Updated: Keep an eye on your email for newsletters and updates on upcoming events and exclusive offers.",
    "Connect with Us: Follow us on social media to stay connected with our community and get the latest news and updates.",
    "Share Your Stories: We love hearing about your fishing adventures! Share your photos and stories with us, and you might even be featured on our website or social media.",
  ];

  const handleData = async () => {
    try {
      const sessionId = location.search.split("=")[1].split("&")[0];
      const membershipId = location.search.split("=")[2];

      const res = await checkPaymentStatus({ sessionId });

      if (res.paymentStatus == "succeeded") {
        updatePlan({ planId: membershipId })
          .then((res) => {
            setUser({
              ...currentUser,
              plan: {
                ...currentUser.plan,
                planID: res.plan,
                expires: res.expires,
              },
            });
          })
          .catch((err) => {});
      } else {
        navigate("/membership");
      }
    } catch (err) {}
  };

  useEffect(() => {
    handleData();
  }, []);

  return (
    <div>
      <div className="container text-white">
        <div className="mt-5 w-75 text-center mx-auto">
          <h2 className="mb-3 mt-4 text-secondary">
            THANK YOU FOR JOINING OUR MEMBERSHIP
          </h2>
          <p className="font-mont">
            Thank you for becoming a member of Fishing the Atlantic. We're
            thrilled to have you join our community of fishing enthusiasts. Your
            support is invaluable and allows us to continue our mission of
            promoting and celebrating the art of fishing.
          </p>
        </div>
        <div className="mt-5">
          <h2 className="mb-3 mt-4 text-secondary">MEMBERSHIP BENIFITS</h2>
          {currentUser?.plan?.planID?.features?.map((feature) => (
            <div className="d-flex align-items-center gap-2 mb-2 text-white font-mont">
              <p
                className="fw-bold m-0 rounded-circle bg-white"
                style={{ padding: "3px" }}
              ></p>
              <p className="m-0 p-0 font-mont">{feature}</p>
            </div>
          ))}
        </div>

        <div className="mt-5 col-12 col-md-6">
          <p className="h2 mb-3 mt-4 text-secondary">Next Steps:</p>
          <p className="font-mont">
            To make the most of your membership, here are a few steps you can
            take:
          </p>
          {nextSteps.map((feature) => (
            <div className="d-flex align-items-center gap-2 mb-2 text-white">
              <p
                className="fw-bold m-0 rounded-circle bg-white"
                style={{ padding: "3px" }}
              ></p>
              <p className="m-0 p-0 font-mont">{feature}</p>
            </div>
          ))}
        </div>
        <div className="mt-5 col-12 col-md-6">
          <p className="h2 mb-3 mt-4 text-secondary">Contact Us</p>
          <p className="font-mont">
            If you have any questions, suggestions, or need assistance with
            anything related to your membership, please don't hesitate to
            contact our support team. We're here to help. <br />
            <br />
            <br />
            Once again, thank you for choosing to be a part of Fishing the
            Atlantic. We look forward to sharing the joy of fishing with you and
            helping you make the most of your angling adventures. Tight lines
            and happy fishing!{" "}
          </p>
        </div>
        <div className="bg-secondary w-50 rounded-5 m-auto px-4 contact-main-div d-flex flex-column justify-content-center mt-5">
          <div className="mt-5">
            <h1 className="text-center mb-4 text-dark font-mont">
              Get in touch with us
            </h1>
            <div className="d-flex flex-column gap-4">
              <div className="d-flex justify-content-between gap-3 gap-md-5 flex-wrap flex-md-nowrap">
                <div className="w-100">
                  <input
                    type="text"
                    className="contact-input border-0 form-control-lg w-100 font-russo"
                    id="name"
                    name="name"
                    placeholder="NAME:"
                    required
                  />
                </div>
                <div className="w-100">
                  <input
                    type="email"
                    className="contact-input border-0 form-control-lg w-100 font-russo"
                    id="email"
                    name="email"
                    placeholder="EMAIL:"
                    required
                  />
                </div>
              </div>
              <textarea
                className="contact-input border-0 form-control-lg font-russo"
                id="message"
                name="message"
                rows="4"
                placeholder="MESSAGE:"
                required
              ></textarea>

              <button className="btn bg-white m-auto px-4">SEND</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
