import React, { useContext, useEffect } from "react";
import Navbar from "./components/Navbar";
import Footer from "./components/Footer";
import { BrowserRouter, Routes, Route, useLocation } from "react-router-dom";
import Home from "./pages/home";
import About from "./pages/about";
import Contact from "./pages/contact";
import EventBook from "./pages/eventBook";
import Membership from "./pages/membership";
import Merch from "./pages/merch";
import ProductDetails from "./pages/productDetails";
import Events from "./pages/events";
import "./assets/css/App.css";
import Products from "./pages/products";
import PageTop from "./components/PageTop";
import Checkout from "./pages/checkout";
import { AuthContext } from "./contexts/userContext";
import MembershipCheckout from "./pages/membershipCheckout";
import Orders from "./pages/orders";
import OrderPreview from "./pages/orderPreview";
import EventBookingsDetails from "./pages/eventBookingsDetails";
import AdminRoute from "./routes/AdminRoute";
import RequireAuth from "./routes/RequireAuth";
import MembershipSuccess from "./pages/membershipSuccess";
import PurchaseHistory from "./pages/purchaseHistory";
import AccountSettings from "./pages/accountSettings";
import UserMembership from "./pages/userMembership";

function App() {
  const { checkAuth } = useContext(AuthContext);

  const location = useLocation();

  useEffect(() => {
    checkAuth();
  }, []);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, [location.pathname]);

  return (
    <div className="bg-dark">
      <Navbar />
      <div className="mb-5">
        <PageTop />
      </div>
      <div className="mx-3 mx-sm-5 mx-xxl-5 px-xxl-5">
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/about" element={<About />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/products" element={<Products />} />
          <Route path="/events" element={<Events />} />
          <Route path="/event-book/:eventId" element={<EventBook />} />
          <Route path="/membership" element={<Membership />} />
          <Route path="/merch" element={<Merch />} />
          <Route
            path="/product-details/:productId"
            element={<ProductDetails />}
          />
          <Route
            path="/checkout"
            element={
              <RequireAuth redirectTo={"/login"}>
                <Checkout />
              </RequireAuth>
            }
          />
          <Route
            path="/membership-checkout/:membershipId"
            element={
              <RequireAuth redirectTo={"/login"}>
                <MembershipCheckout />
              </RequireAuth>
            }
          />{" "}
          <Route
            path="/membership/success"
            element={
              <RequireAuth redirectTo={"/login"}>
                <MembershipSuccess />
              </RequireAuth>
            }
          />
          <Route
            path="/orders"
            element={
              <AdminRoute redirectTo={"/"}>
                <Orders />
              </AdminRoute>
            }
          />
          <Route path="/order-preview/:orderId" element={<OrderPreview />} />
          <Route
            path="/bookings/:eventId"
            element={
              <AdminRoute redirectTo={"/"}>
                <EventBookingsDetails />
              </AdminRoute>
            }
          />
          <Route
            path="/purchase-history"
            element={
              <RequireAuth redirectTo={"/login"}>
                <PurchaseHistory />
              </RequireAuth>
            }
          />
          <Route
            path="/user-membership"
            element={
              <RequireAuth redirectTo={"/login"}>
                <UserMembership />
              </RequireAuth>
            }
          />
          <Route
            path="/account-settings"
            element={
              <RequireAuth redirectTo={"/login"}>
                <AccountSettings />
              </RequireAuth>
            }
          />
        </Routes>
        <div className="mt-5">
          <Footer />
        </div>
      </div>
    </div>
  );
}

export default App;
