import React, { useContext } from "react";
import { AuthContext } from "../contexts/userContext";
import Moment from "react-moment";
import DiscordIcon from "../assets/Images/discord2.svg";
import moment from "moment";

export default function UserMembership() {
  const { currentUser } = useContext(AuthContext);

  const discordFeatures = [
    "Meet anglers from around the world who share your love for fishing. Swap stories, tips, and tricks with people who get your passion.",
    "Our Discord is home to seasoned anglers who are ready to share their knowledge. Get expert advice and insights to improve your fishing game.",
    "Stay up-to-date with the latest fishing news, events, and exclusive club updates. Don't miss out on exciting opportunities and tournaments.",
    "Forge new friendships, fishing buddies, and connections in our friendly and welcoming community.",
    "Share your thoughts, suggestions, and feedback to help us make the club even better.",
  ];

  return (
    <div>
      {currentUser?.plan?.planID ? (
        <div className="container">
          <h2 className="text-secondary text-center">MEMBERSHIP DETAILS</h2>
          <div className="w-75 mx-auto mt-5">
            <div className="d-fflex flex-column text-white">
              <div className="d-flex">
                <p className="border p-3" style={{ flex: 0.5 }}>
                  MEMBERSHIP TYPE
                </p>
                <p className="border p-3" style={{ flex: 0.5 }}>
                  {currentUser.plan.planID.title}
                </p>
              </div>
              <div className="d-flex">
                <p className="border p-3" style={{ flex: 0.5 }}>
                  MEMBERSHIP START DATE
                </p>
                <p className="border p-3" style={{ flex: 0.5 }}>
                  {moment(currentUser.plan.updatedAt).format("DD MMM, YYYY")}
                  {/* <Moment format="DD MMM, YYYY">
                    {currentUser.plan.updatedAt}
                  </Moment> */}
                </p>
              </div>
              <div className="d-flex">
                <p className="border p-3" style={{ flex: 0.5 }}>
                  MEMBERSHIP EXPIRY DATE
                </p>
                <p className="border p-3" style={{ flex: 0.5 }}>
                  {moment(currentUser.plan.expires).format("DD MMM, YYYY")}
                  {/* <Moment format="DD MMM, YYYY">
                    {currentUser.plan.expires}
                  </Moment> */}
                </p>
              </div>
            </div>
          </div>
          <div className="mt-5">
            <h2 className="mb-3 mt-4 text-secondary">MEMBERSHIP BENIFITS</h2>
            {currentUser?.plan?.planID?.features.map((feature) => (
              <div className="d-flex align-items-center gap-2 mb-2 text-white font-mont">
                <p
                  className="fw-bold m-0 rounded-circle bg-white"
                  style={{ padding: "3px" }}
                ></p>
                <p className="m-0 p-0 font-mont">{feature}</p>
              </div>
            ))}
          </div>
          <div className="mt-5 col-12 col-md-6"></div>
          <div className="mt-5 col-12 col-md-6">
            <h2 className="mb-3 mt-4 text-secondary">
              Why Join Our Discord Community?
            </h2>
            {discordFeatures.map((feature) => (
              <div className="d-flex align-items-center gap-2 mb-2 text-white font-mont">
                <p
                  className="fw-bold m-0 rounded-circle bg-white"
                  style={{ padding: "3px" }}
                ></p>
                <p className="m-0 p-0 font-mont">{feature}</p>
              </div>
            ))}
          </div>
          <div className="mt-5 col-12 col-md-6 font-mont">
            <p className="text-white">
              Click the button to join our Discord community and take your
              fishing experience to the next level.
            </p>
          </div>
          <div className="mt-5">
            <button
              className="btn d-flex gap-3"
              style={{ background: "#D9D9D9" }}
            >
              <img src={DiscordIcon} />
              JOIN DISCORD
            </button>
          </div>
        </div>
      ) : (
        <h2 className="text-center text-white">No Active Membership</h2>
      )}
    </div>
  );
}
