import React, { useEffect, useState } from "react";
import {
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from "reactstrap";

export default function UpdateBannerModal({ open, oncancel, onconfirm }) {
  const [image, setimage] = useState([]);

  const [apiCalled, setapiCalled] = useState(false);

  const handleOnConfirm = () => {
    if (image.length > 1) {
      setapiCalled(true);
      onconfirm({
        image,
      });
    }
  };

  useEffect(() => {}, [image]);

  return (
    <Modal isOpen={open} centered>
      <ModalHeader>Update Banner</ModalHeader>
      <ModalBody>
        <div className="d-flex flex-column gap-2">
          <p>Desktop Banner:</p>
          <Input
            accept="image/*"
            type="file"
            onChange={(e) => {
              const newArray = [...image];
              newArray[0] = e.target.files[0];
              setimage(newArray);
            }}
          />
        </div>
        <div className="d-flex flex-column gap-2 mt-3">
          <p>Mobile Banner:</p>
          <Input
            accept="image/*"
            type="file"
            onChange={(e) => {
              const newName =
                e.target.files[0].name.split(".")[0] +
                "_sm." +
                e.target.files[0].name.split(".")[1];

              const renamedFile = new File([e.target.files[0]], newName, {
                type: e.target.files[0].type,
              });
              const newArray = [...image];
              newArray[1] = renamedFile;
              setimage(newArray);
            }}
          />
        </div>
      </ModalBody>
      <ModalFooter>
        <button
          className="btn bg-primary text-white"
          onClick={handleOnConfirm}
          disabled={apiCalled}
        >
          Update Banner
        </button>{" "}
        <Button color="secondary" onClick={oncancel} disabled={apiCalled}>
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
}
