import React from "react";
import "../../assets/css/Merch.css";
import { useState } from "react";
import HomeYoutubeCard from "./HomeYoutubeCard";
import youtube_logo from "../../assets/Images/youtube_logo.png";
import { Link } from "react-router-dom";

function HomeYoutube({ socialPosts }) {
  return (
    <div className="merch-bg ">
      <div className="text-center  ">
        <div className="align-items-center d-flex justify-content-center mb-5 gap-2">
          <img
            style={{ width: "130px", height: "90px" }}
            src={youtube_logo}
          ></img>
          <div className="text-start">
            <p className="text-white mb-1 m-0 p-0 h4 fw-normal font-mont">
              YOUTUBE
            </p>

            <p className=" text-secondary h2 m-0 p-0"> VIDEOS</p>
          </div>

          <div />
        </div>

        <div className="container-fluid">
          <div className="d-block d-sm-flex justify-content-center gap-3 flex-wrap flex-sm-nowrap">
            {socialPosts.map((e, i) => {
              return <HomeYoutubeCard youtube={e} />;
            })}
          </div>
        </div>
      </div>
      <button className="btn bg-white d-flex mt-5 justify-content-center align-items-center mx-auto">
        <img style={{ width: "30px" }} src={youtube_logo}></img>
        <Link
          className="text-dark text-decoration-none"
          to={"https://youtube.com/@fishingtheatlantic?si=9Gidrwmr5EXO_7mF"}
          target="_blank"
        >
          <p className="m-0 p-0 h5">@FISHINGTHEATLANTIC</p>
        </Link>
      </button>
    </div>
  );
}

export default HomeYoutube;
