import React, { useContext, useState } from "react";
import UserDeliveryDetails from "../components/UserDeliveryDetails";
import { updateUser } from "../api/user";
import { AuthContext } from "../contexts/userContext";

export default function AccountSettings() {
  const [message, setmessage] = useState("");

  const { setUser, currentUser } = useContext(AuthContext);
  const [username, setusername] = useState(currentUser?.username);
  const [oldPassword, setoldPassword] = useState("");
  const [newPassword, setnewPassword] = useState("");

  const handleUpdateUser = ({ data }) => {
    updateUser({ deliveryAddress: data })
      .then((res) => {
        setUser(res);
        window.scrollTo(0, 300);
        // window.location.reload();
        setmessage("INFORMATION UPDATED");
        setTimeout(() => {
          setmessage("");
        }, 3000);
      })
      .catch((err) => {});
  };

  const handleSecurityUpdate = () => {
    if (username && oldPassword && newPassword) {
      updateUser({ username, oldPassword, newPassword })
        .then((res) => {
          setUser(res);
          window.scrollTo(0, 300);
          // window.location.reload();
          setmessage("INFORMATION UPDATED");
          setTimeout(() => {
            setmessage("");
          }, 3000);
        })
        .catch((err) => {
          window.scrollTo(0, 300);
          setmessage(err.response.data);
        });
    }
  };

  return (
    <div>
      <p className="text-secondary text-center fw-bold h2">ACCOUNT SETTING</p>
      <div className="col-12 col-md-5 mt-5 pt-5">
        <p className="text-secondary text-center fw-bold h4">{message}</p>
        <UserDeliveryDetails
          SubmitBtn={SubmitBtn}
          setmessage={setmessage}
          onsubmit={handleUpdateUser}
        />
        <div>
          <h3 className="text-white">Security</h3>
          <div>
            <label className="text-white h5 mt-4">Username:</label>
            <input
              className="form-control-lg bg-muted w-100 form-control mt-1  text-dark"
              type="text"
              placeholder="username"
              value={username}
              onChange={(e) => {
                setusername(e.target.value);
              }}
            />
            <label className="text-white h5 mt-4">Old Password:</label>
            <input
              className="form-control-lg bg-muted w-100 form-control mt-1 text-dark"
              type="password"
              placeholder="Old Password"
              value={oldPassword}
              onChange={(e) => {
                setoldPassword(e.target.value);
              }}
            />
            <label className="text-white h5 mt-4">New Password:</label>
            <input
              className="form-control-lg bg-muted w-100 form-control mt-1 text-dark"
              type="password"
              placeholder="New Password"
              value={newPassword}
              onChange={(e) => {
                setnewPassword(e.target.value);
              }}
            />
            <button
              className="mb-5 btn w-100 bg-muted btn-lg  text-dark fw-bold mt-5"
              onClick={handleSecurityUpdate}
            >
              Update Security Details
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}

const SubmitBtn = ({ clickHandler }) => {
  return (
    <div className="d-flex flex-column align-items-center justify-content-center">
      <button
        className="mb-5 btn w-100 mt-4 bg-muted btn-lg  text-dark fw-bold"
        onClick={clickHandler}
      >
        Update Delivery Details
      </button>
    </div>
  );
};
