import React, { useContext, useEffect, useState } from "react";
import HomeMembership from "../components/Home/HomeMembership";
import AddMembershipModal from "../components/modals/membership/MembershipModal";
import { createMembership, getMemberships } from "../api/plan";
import { AuthContext } from "../contexts/userContext";
import { useNavigate } from "react-router-dom";

function Membership() {
  const navigate = useNavigate();
  const [memberships, setmemberships] = useState([]);

  const { currentUser } = useContext(AuthContext);

  const [addMembershipModalOpen, setaddMembershipModalOpen] = useState(false);

  const handleAddMembership = ({ data }) => {
    createMembership({ data })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        setaddMembershipModalOpen(false);
      });
  };

  const handleGetMemberships = () => {
    getMemberships()
      .then((res) => {
        res.map((member) => {
          member.isFullDescription = true;
        });
        setmemberships(res);
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetMemberships();
  }, []);

  return (
    <div>
      <AddMembershipModal
        type={"Membership"}
        open={addMembershipModalOpen}
        onconfirm={handleAddMembership}
        oncancel={() => setaddMembershipModalOpen(false)}
      />

      <div className="text-center">
        <p className=" text-secondary h2">FISHING THE ATLANTIC MERCH</p>
        <p className="text-white mb-2 h4">Exclusive Membership</p>
        <div className="font-mont fw-bolder text-white m-auto col-12 col-lg-6">
          <p className="my-3">
            Welcome to the community that bonds, thrives, and dives into the
            heart of the Atlantic. Experience fishing like never before with our
            comprehensive membership plans that cater to every angler's need.
          </p>
          <p className="text-secondary">
            We are excited to introduce two innovative membership tiers for our
            dedicated fishing enthusiasts. We've got a reel-in deal for
            everyone!
          </p>
        </div>
      </div>
      <p className="h1 text-white text-center my-5 pt-5">CHOOSE YOUR PLAN</p>
      {currentUser?.isAdmin && (
        <div className="text-end mb-5">
          <button
            className="btn bg-white btn-lg"
            onClick={() => setaddMembershipModalOpen(true)}
          >
            Add Membership
          </button>
        </div>
      )}
      <HomeMembership membership={memberships} />

      <div className="text-center mt-5 pt-5">
        <p className="text-white mb-2 h5 text-secondary">
          Join us at Fishing the Atlantic, and become part of our network
        </p>
        <div className="font-mont fw-bolder text-white m-auto col-12 col-lg-7">
          <p className="my-3">
            FTA is dedicated to providing a seamless fishing experience while
            creating a community that promotes personal growth. As a member, you
            will not only have the opportunity to explore the Atlantic but also
            to build relationships, improve your personal skills, and enjoy the
            thrill of the sport. Your catch isn't just the fish, it's the total
            experience.
          </p>
          {!currentUser && (
            <button
              className="btn bg-secondary fw-bold btn-lg"
              onClick={() => navigate("/register")}
            >
              SIGN UP NOW
            </button>
          )}
        </div>
      </div>
    </div>
  );
}
export default Membership;
