import React, { useContext, useEffect, useState } from "react";
import "../assets/css/checkout.css";
import { Link, useNavigate, useParams } from "react-router-dom";
import { AuthContext } from "../contexts/userContext";
import CheckoutLeft from "../components/checkout/CheckoutLeft";
import CheckoutRight from "../components/checkout/CheckoutRight";
import { getMembershipById } from "../api/plan";
import MembershipCheckoutLeft from "../components/membershipCheckout/membershipCheckoutLeft";
import MembershipCheckoutRight from "../components/membershipCheckout/membershipCheckoutRight";

export default function MembershipCheckout() {
  const navigate = useNavigate();
  const { membershipId } = useParams();
  const [membership, setmembership] = useState(null);

  const { currentUser } = useContext(AuthContext);

  const handleGetMembership = () => {
    getMembershipById({ id: membershipId })
      .then((res) => {
        if (res._id == currentUser?.plan.planID) {
          navigate("/membership");
        }
        setmembership(res);
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetMembership();
  }, []);

  return (
    <div>
      <div className="text-center text-secondary">
        <h2>
          <u>MEMBERSHIP CHECK OUT</u>
        </h2>
      </div>
      {membership && (
        <div className="container mt-5 pt-5 font-mont">
          <div className="row">
            <div className="checkout-right-side text-white px-0 px-lg-4 py-5 py-lg-0 mt-4">
              <MembershipCheckoutRight membership={membership} />
            </div>

            <div className="checkout-left-side mt-5 col-12 col-lg-6">
              <MembershipCheckoutLeft membership={membership} />
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
