import { axiosClient } from "./axios";

export const getMemberships = async () => {
  try {
    const res = await axiosClient.get("/plan");

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getMembershipById = async ({ id }) => {
  try {
    const res = await axiosClient.get(`/plan/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const createMembership = async ({ data }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.post("/plan", data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const updateMembership = async ({ data, id }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.put(`/plan/${id}`, data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const deleteMembership = async ({ id }) => {
  try {
    const res = await axiosClient.delete(`/plan/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
