import React, { useContext, useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { checkLoggedIn, login } from "../api/user";
import { AuthContext } from "../contexts/userContext";
import GoogleIcon from "../assets/Images/google-icon.svg";

export default function Login() {
  const navigate = useNavigate();

  const { setUser } = useContext(AuthContext);

  const [email, setemail] = useState("");
  const [password, setpassword] = useState("");
  const [error, seterror] = useState(false);

  const handleSubmit = () => {
    login({ email, password })
      .then((res) => {
        setUser({ user: res.user, access_token: res.access_token });
        navigate("/");
      })
      .catch((err) => {
        seterror(true);
        setTimeout(() => {
          seterror(false);
        }, 2000);
      });
  };

  return (
    <div className="bg-dark text-white">
      <div className="container d-flex flex-column justify-content-center vh-100">
        <div className="row">
          <div className="col-11 col-sm-8 m-auto col-xl-4">
            <div className="login_form">
              <div className="login_form_header">
                <p className="text-center h2">LOGIN</p>
              </div>
              <div className="login_form_body mt-4 d-flex flex-column gap-2">
                <div className="add_product_input_div">
                  <label className="form-label">Email address</label>
                  <input
                    value={email}
                    onChange={(e) => {
                      setemail(e.target.value);
                    }}
                    type="email"
                    name="email"
                    className="form-control"
                    id="email"
                    aria-describedby="emailHelp"
                    placeholder="Enter email"
                  />
                </div>
                <div className="add_product_input_div">
                  <label className="form-label">Password</label>
                  <input
                    value={password}
                    onChange={(e) => {
                      setpassword(e.target.value);
                    }}
                    type="password"
                    name="password"
                    className="form-control"
                    id="password"
                    placeholder="Password"
                  />
                </div>

                <div className="d-flex justify-content-center w-100 mt-4">
                  <button
                    className="btn btn-primary w-100"
                    onClick={handleSubmit}
                  >
                    Login
                  </button>
                </div>
                <a
                  href={`${process.env.REACT_APP_BASE_URL}/auth/google`}
                  className={`position-relative btn bg-white text-dark w-100 login-btn mt-4`}
                >
                  <span className={`fw-bolder loading-text small`}>
                    <div className="d-flex h-100 w-100 align-items-center justify-content-center gap-2">
                      <img
                        src={GoogleIcon}
                        className="mx-2"
                        style={{ height: "25px" }}
                      />
                      <span className="fw-bolder text-nowrap h6 m-0">
                        Continue with Google
                      </span>
                    </div>
                  </span>
                </a>

                <p className="mt-3">
                  Don't have an account?{" "}
                  <Link to="/register" className="text-secondary">
                    Register
                  </Link>
                </p>
                {error && (
                  <div
                    class="alert alert-danger mt-4 w-50 m-auto text-center"
                    role="alert"
                  >
                    <span className="small">INVALID CREDENTIALS</span>
                  </div>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
