import { axiosClient } from "./axios";

export const getCart = async () => {
  try {
    const res = await axiosClient.get(`/cart`);
    return res.data;
  } catch (err) {
    throw err;
  }
};

export const updateCart = async ({ products, total }) => {
  try {
    const res = await axiosClient.put(`/cart`, { products, total });

    return res.data;
  } catch (err) {
    throw err;
  }
};
