import React, { useContext } from "react";
import "../../assets/css/Product_Card.css";
import "../../assets/css/MembershipCard.css";
import { useState } from "react";
import MembershipModal from "../modals/membership/MembershipModal";
import ConfrimMdal from "../modals/ConfirmModal";
import { deleteMembership, updateMembership } from "../../api/plan";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../contexts/userContext";

function HomeMembershipCard({ member }) {
  const navigate = useNavigate();
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const { currentUser } = useContext(AuthContext);

  const [editModalOpen, seteditModalOpen] = useState(false);
  const [confirmModalOpen, setconfirmModalOpen] = useState(false);

  const handleEditMembership = ({ data }) => {
    updateMembership({ data, id: member._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        seteditModalOpen(false);
      });
  };
  const handleDeleteMembership = () => {
    deleteMembership({ id: member._id })
      .then((res) => {
        console.log(res);
        window.location.reload();
      })
      .catch((err) => {
        console.log(err);
        setconfirmModalOpen(false);
      });
  };

  return (
    <div className="border rounded overflow-hidden">
      <MembershipModal
        type={"Membership"}
        open={editModalOpen}
        onconfirm={handleEditMembership}
        oncancel={() => seteditModalOpen(false)}
        data={member}
      />
      <ConfrimMdal
        title={"CONFIRM DELETE"}
        description={"Are you sure you want to delete this memberhsip?"}
        open={confirmModalOpen}
        onconfirm={handleDeleteMembership}
        oncancel={() => setconfirmModalOpen(false)}
      />
      <div className="row text-white membership-card">
        <div
          id={member._id}
          className="col-12 col-md-5 position-relative membership-left"
          style={{
            background: `url(${baseUrl + member.image})`,
            backgroundRepeat: "no-repeat",
            backgroundPosition: "center",
            backgroundSize: "cover",
            // maxHeight: "500px",
          }}
        >
          <div
            className="position-absolute h-100 w-100  "
            style={{ left: 0, background: "rgba(0,0,0,0.5)", zIndex: 1 }}
          ></div>
          <div
            className="d-flex align-items-center justify-content-center h-100 position-relative"
            style={{ zIndex: 2 }}
          >
            <div className="h-75 d-flex align-items-center flex-column justify-content-between text-center">
              <div>
                <h2 className="mb-2 ">{member.title}</h2>
                <h4>
                  {member.duration} {member.duration > 1 ? "YEARS" : "YEAR"}
                </h4>
              </div>
              <h1 className="text-secondary">${member.price}</h1>
            </div>
          </div>
        </div>
        <div className="col-12 col-md-6 membership-right">
          <div className="py-4 px-3 p-md-4 d-flex flex-column justify-content-between h-100">
            <div>
              <h4 className="mb-2">{member.description}</h4>
              <h5 className="mb-3 mt-4 text-secondary">INCLUDES</h5>
              {member.features.map((feature) => (
                <div className="d-flex align-items-center gap-2 mb-2">
                  <p
                    className=" h5 fw-bold m-0 rounded-circle bg-white"
                    style={{ padding: "3px" }}
                  ></p>
                  <p className="h5 m-0 p-0 font-mont">{feature}</p>
                </div>
              ))}
            </div>
            <div>
              {!member.isFullDescription ? (
                <div className="d-flex justify-content-center justify-content-md-start">
                  <button
                    className="btn bg-white text-dark mt-4"
                    onClick={() => navigate(`/membership#${member._id}`)}
                  >
                    FIND OUT MORE
                  </button>
                </div>
              ) : (
                <div className="d-flex justify-content-center justify-content-md-start">
                  {currentUser ? (
                    <button
                      disabled={currentUser?.plan.planID?._id == member._id}
                      className="btn bg-white text-dark mt-4 "
                      onClick={() =>
                        navigate(`/membership-checkout/${member._id}`)
                      }
                    >
                      {currentUser?.plan.planID?._id != member._id
                        ? "Buy Now"
                        : "Active Plan"}
                    </button>
                  ) : (
                    <button
                      className="btn bg-white text-dark mt-4 "
                      onClick={() => navigate(`/register`)}
                    >
                      Signup to Buy Membership
                    </button>
                  )}
                </div>
              )}
              {currentUser?.isAdmin && (
                <div className="d-flex gap-3 flex-wrap justify-content-center justify-content-md-start mt-3">
                  <div className="d-flex justify-content-center justify-content-md-start">
                    <button
                      className="btn bg-white text-dark"
                      onClick={() => seteditModalOpen(true)}
                    >
                      EDIT
                    </button>
                  </div>
                  <div className="d-flex justify-content-center justify-content-md-start">
                    <button
                      className="btn bg-danger text-white"
                      onClick={() => setconfirmModalOpen(true)}
                    >
                      DELETE
                    </button>
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default HomeMembershipCard;
