import React, { useContext, useState } from "react";
import { AuthContext } from "../../contexts/userContext";

export default function CheckoutRight() {
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const { currentUser, cart, setcart } = useContext(AuthContext);

  return (
    <div>
      <div className="mx-xl-3">
        <div className="d-flex flex-column  gap-5">
          {cart.products.map((pro) => (
            <div className="d-flex gap-3">
              <img
                className="w-25 rounded-3"
                style={{ minWidth: "111px", objectFit: "cover" }}
                src={baseUrl + pro.product.productImages[0]}
              ></img>
              <div className="d-flex flex-column gap-3">
                <div className="d-flex flex-column">
                  <p className="">{pro.product.name}</p>
                  <p className=" text-wrap" style={{ width: "90%" }}></p>
                </div>
                <p className="">${pro.product.price}</p>
              </div>
            </div>
          ))}
          <div className="d-flex justify-content-between w-50">
            <p>Subtotal</p>
            <p>USD ${cart.total}</p>
          </div>
          {currentUser?.plan.planID && (
            <div className="d-flex justify-content-between w-50">
              <p>
                Membership Discount <br />
                {/* <span className="xsmall text-secondary">(One Time)</span> */}
              </p>
              <p className="text-nowrap">USD $10</p>
            </div>
          )}
          <div className="d-flex justify-content-between w-50">
            <div className="d-flex flex-column">
              <p>Total</p>
              <p className="xsmall">Including Tax</p>
              <p className="text-secondary xsmall">(Including Shipping Fee)</p>
            </div>

            <p className="text-nowrap">
              USD ${currentUser?.plan.planID ? cart.total - 10 : cart.total}
            </p>
          </div>

          <div className=" ">
            <input
              className="w-50  text-light form-control form-control-lg bg-transparent input-white"
              placeholder="Discount Code"
            ></input>
          </div>
        </div>
      </div>
    </div>
  );
}
