import React from "react";
import RightIcon from "../assets/Images/right-icon.svg";

export default function Pagination({
  skip,
  hasMoreProducts,
  handleGetNext,
  handleGetPrev,
  isloading,
}) {
  return (
    <div className="d-flex justify-content-center">
      <button
        className="btn d-flex align-items-center border-0 gap-3"
        disabled={skip == 0 || isloading}
        onClick={handleGetPrev}
      >
        <img src={RightIcon} style={{ transform: "rotate(180deg)" }} />
        <p className="m-0 p-0 text-white">Previous</p>
      </button>
      <button
        disabled={!hasMoreProducts || isloading}
        className="btn d-flex align-items-center border-0 gap-3"
        onClick={handleGetNext}
      >
        <p className="m-0 p-0 text-white">NEXT</p>
        <img src={RightIcon} />
      </button>
    </div>
  );
}
