import { axiosClient } from "./axios";

export const getEvents = async ({ limit, skip }) => {
  try {
    const res = await axiosClient.get(`/event?limit=${limit}&&skip=${skip}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getEventById = async ({ id }) => {
  try {
    const res = await axiosClient.get(`/event/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const createEvent = async ({ data }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.post("/event", data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const updateEvent = async ({ data, id }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.put(`/event/${id}`, data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const deleteEvent = async ({ id }) => {
  try {
    const res = await axiosClient.delete(`/event/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
