import React, { useEffect } from "react";
import Merch from "../components/Home/HomeMerch";
import HomeMembership from "../components/Home/HomeMembership";
import HomeEvents from "../components/Home/HomeEvents";
import { useState } from "react";
import { getProducs } from "../api/product";
import black_hole_logo from "../assets/Images/black_hole_logo.png";
import { getMemberships } from "../api/plan";
import { getEvents } from "../api/event";
import { useNavigate } from "react-router-dom";
import SocialPosts from "../components/Home/SocialPosts";
import { getSocialPosts } from "../api/socialPost";

function Home() {
  const navigate = useNavigate();

  const [merch, setmerch] = useState([]);
  const [products, setproducts] = useState([]);
  const [memberships, setmemberships] = useState([]);
  const [events, setevents] = useState([]);
  const [socialPosts, setsocialPosts] = useState([]);

  const [loading, setloading] = useState(true);

  const handleGetSocialPosts = () => {
    getSocialPosts()
      .then((res) => {
        setsocialPosts(res);
      })
      .catch((err) => {});
  };

  const handleGetMerch = () => {
    getProducs({ limit: 4, skip: 0, type: "merch" })
      .then((res) => {
        setmerch(res);
      })
      .catch((err) => {});
  };

  const handleGetProducts = () => {
    getProducs({ limit: 4, skip: 0, type: "product" })
      .then((res) => {
        setproducts(res);
      })
      .catch((err) => {});
  };

  const handleGetMemberships = () => {
    getMemberships()
      .then((res) => {
        res.map((member) => {
          member.description = "Here’s what we offer you";
          member.isFullDescription = false;
        });
        setmemberships(res);
      })
      .catch((err) => {});
  };

  const handleGetAllEvents = () => {
    getEvents({ limit: 2, skip: 0 })
      .then((res) => {
        setevents(res);
      })
      .catch((err) => {
        setloading(false);
      });
  };

  useEffect(() => {
    Promise.all([
      handleGetMerch(),
      handleGetProducts(),
      handleGetMemberships(),
      handleGetAllEvents(),
      handleGetSocialPosts(),
    ])
      .then((res) => {
        setloading(false);
      })
      .catch((err) => {
        setloading(false);
      });
  }, []);

  return (
    <>
      <div className="mb-5 pb-5">
        <div>
          <div className="merch-title mb-5 pb-5">
            <p className="text-white mb-2 small">FISHING THE ATLANTIC MERCH</p>
            <p className=" text-secondary h2">MERCH</p>
          </div>
          <Merch merch={merch} />
          <div className="text-center mt-5">
            <button
              className="btn bg-white text-dark"
              onClick={() => navigate("/merch")}
            >
              VIEW ALL PRODUCTS
            </button>
          </div>
        </div>

        <div className="mt-5 pt-5">
          <div className="merch-title mb-5">
            <p className="text-white small mb-2">
              FISHING THE ATLANTIC MEMBERSHIP
            </p>
            <p className=" text-secondary h2"> BECOME A MEMBER</p>
          </div>
          <HomeMembership membership={memberships} />
        </div>
        <div className="mt-5 pt-5">
          <div className="merch-title mb-5">
            <p className="text-white small mb-2">PRODUCTS</p>

            <p className=" text-secondary h4 mb-5"> PREMIUM RODS</p>
            <div>
              <img
                style={{ height: "auto", width: "200px" }}
                src={black_hole_logo}
              ></img>
            </div>
          </div>
          <Merch merch={products} />
          <div className="d-flex mt-5 justify-content-center">
            <button
              className="btn bg-white"
              onClick={() => navigate("/products")}
            >
              VIEW ALL PRODUCTS
            </button>
          </div>
        </div>
        <div className="mt-5 pt-5">
          <div className="text-center">
            <p className=" text-white small mb-2">UPCOMING</p>

            <p className=" text-secondary h2 mb-5"> EVENTS AND CHARTERS</p>
          </div>
          <HomeEvents events={events} />
          <div className="d-flex justify-content-center justify-content-md-start">
            <button
              className="btn bg-white text-dark mt-5 m-auto"
              onClick={() => navigate("/events")}
            >
              VIEW ALL EVENTS
            </button>
          </div>
        </div>
        <div className="mt-5 pt-5">
          <SocialPosts socialPosts={socialPosts} />
        </div>
      </div>
    </>
  );
}

export default Home;
