import { axiosClient } from "./axios";

export const addProduct = async ({ data }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.post("/product", data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getProducs = async ({ limit, skip, type }) => {
  try {
    const res = await axiosClient.get(
      `/product?skip=${skip}&&limit=${limit}&&type=${type}`
    );

    return res.data;
  } catch (err) {
    throw err;
  }
};
export const getProduct = async ({ id }) => {
  try {
    const res = await axiosClient.get(`/product/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
export const updateProduct = async ({ data, id }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.put(`/product/${id}`, data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };
    return res.data;
  } catch (err) {
    throw err;
  }
};
export const deleteProduct = async ({ productId }) => {
  try {
    const res = await axiosClient.delete(`/product/${productId}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
