import React, { useContext, useState } from "react";
import "../assets/css/Product_Card.css";
import EditProductModal from "./modals/Product/EditProductModal";
import ConfirmModal from "./modals/ConfirmModal";
import { deleteProduct, updateProduct } from "../api/product";
import { useLocation, useNavigate } from "react-router-dom";
import { AuthContext } from "../contexts/userContext";

function Product_Card({ product }) {
  const navigate = useNavigate();

  const { currentUser } = useContext(AuthContext);

  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const [editModalOpen, seteditModalOpen] = useState(false);
  const [confirmModalOpen, setconfirmModalOpen] = useState(false);

  const handleUpdateProduct = ({ data }) => {
    updateProduct({ data, id: product._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        seteditModalOpen(false);
      });
  };
  const handleDeleteProduct = () => {
    deleteProduct({ productId: product._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {
        setconfirmModalOpen(false);
      });
  };

  const handleNavigation = () => {
    navigate(`/product-details/${product._id}`);
  };

  return (
    <div className="col-12 col-sm-6 col-lg-3 d-flex justify-content-center flex-column align-items-center position-relative mb-4">
      <EditProductModal
        open={editModalOpen}
        oncancel={() => seteditModalOpen(false)}
        onconfirm={handleUpdateProduct}
        product={product}
      />
      <ConfirmModal
        title={"DELETE PRODUCT"}
        description={"Are you sure you want to delete this product?"}
        open={confirmModalOpen}
        oncancel={() => setconfirmModalOpen(false)}
        onconfirm={handleDeleteProduct}
      />
      <img
        style={{ cursor: "pointer" }}
        onClick={handleNavigation}
        className="product-image rounded"
        src={baseUrl + product.productImages[0]}
      ></img>
      <h4 className="product-title my-2">{product.name}</h4>
      <h3 className="product-price">${product.price}</h3>
      {currentUser?.isAdmin && (
        <div className="mt-4 d-flex justify-content-between w-75 gap-3">
          <button
            className="btn bg-white w-100"
            onClick={() => seteditModalOpen(true)}
          >
            EDIT
          </button>
          <button
            className="btn bg-danger text-white w-100"
            onClick={() => setconfirmModalOpen(true)}
          >
            DELETE
          </button>
        </div>
      )}
    </div>
  );
}

export default Product_Card;
