import React, { useState } from "react";
import {
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from "reactstrap";

export default function EditProductModal({
  open,
  oncancel,
  onconfirm,
  product,
}) {
  const [name, setname] = useState(product.name);
  const [price, setprice] = useState(product.price);
  const [quantity, setquantity] = useState(product.quantity);
  const [images, setimages] = useState([]);
  const [apiCalled, setapiCalled] = useState(false);
  const [sizes, setsizes] = useState(product.sizes.join("\n"));
  const [features, setfeatures] = useState(product.features.join("\n"));

  const handleOnConfirm = () => {
    if (name && price > 0 && quantity > 0) {
      setapiCalled(true);
      onconfirm({
        data: {
          name,
          price,
          quantity,
          createdAt: Date.now(),
          productImages: images,
          sizes: sizes != "" ? sizes.split("\n") : [],
          features: features != "" ? features.split("\n") : [],
        },
      });
    }
  };

  return (
    <Modal isOpen={open} centered>
      <ModalHeader>Add Product</ModalHeader>
      <ModalBody>
        <div className="d-flex flex-column gap-2">
          <Input
            accept="image/*"
            type="file"
            multiple
            onChange={(e) => setimages(e.target.files)}
          />
          <div>
            <Label>Product Name:</Label>
            <Input
              type="text"
              value={name}
              onChange={(e) => setname(e.target.value)}
            />
          </div>
          <div>
            <Label>Product Price:</Label>
            <Input
              type="number"
              value={price}
              onChange={(e) => setprice(e.target.value)}
            />
          </div>
          <div>
            <Label>Product Quantity:</Label>
            <Input
              type="number"
              value={quantity}
              onChange={(e) => setquantity(e.target.value)}
            />
          </div>
          <div>
            <Label>Sizes:</Label>
            <Input
              placeholder="Size 1
Size 2"
              type="textarea"
              value={sizes}
              onChange={(e) => setsizes(e.target.value)}
            />
          </div>
          <div>
            <Label>Features:</Label>
            <Input
              placeholder="feature 1
feature 2"
              type="textarea"
              value={features}
              onChange={(e) => setfeatures(e.target.value)}
            />
          </div>
        </div>
      </ModalBody>
      <ModalFooter>
        <Button color="primary" onClick={handleOnConfirm} disabled={apiCalled}>
          Update Product
        </Button>{" "}
        <Button color="secondary" onClick={oncancel} disabled={apiCalled}>
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
}
