import React, { useState } from "react";
import {
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from "reactstrap";

import DatePicker from "react-datepicker";

import "react-datepicker/dist/react-datepicker.css";
import moment from "moment";
import Moment from "react-moment";

export default function MembershipModal({
  type,
  open,
  oncancel,
  onconfirm,
  data,
}) {
  const [apiCalled, setapiCalled] = useState(false);

  const [title, settitle] = useState(data ? data.title : "");
  const [description, setdescription] = useState(data ? data.description : "");
  const [price, setprice] = useState(data ? data.price : 0);
  const [discount, setdiscount] = useState(0);
  const [duration, setduration] = useState(
    data ? (data.duration ? data.duration : "") : ""
  );
  const [features, setfeatures] = useState(
    data ? data.features.join("\n") : ""
  );
  const [totalBookings, settotalBookings] = useState(
    data ? data.totalBookings : 0
  );
  const [image, setimage] = useState("");
  const [date, setdate] = useState(
    data ? (data.date ? moment(data?.date).format("DD MMMM, YYYY") : "") : ""
  );

  const handleFeaturesChange = (e) => {
    setfeatures(e.target.value);
  };

  const handleOnConfirm = () => {
    if (
      title &&
      description &&
      (duration || date) &&
      features.split("\n").length > 0 &&
      price &&
      (image || data?.image)
    ) {
      const obj = {
        title,
        description,
        price,
        features: features.split("\n"),
        createdAt: Date.now(),
        totalBookings,
        image,
      };

      duration ? (obj["duration"] = duration) : (obj["date"] = date);
      type.toLowerCase() == "membership" && (obj["discount"] = discount);

      setapiCalled(true);
      onconfirm({
        data: obj,
      });
    }
  };

  return (
    <Modal isOpen={open} centered>
      <ModalHeader>{data ? `Update ${type}` : `Add ${type}`}</ModalHeader>
      <ModalBody>
        <div className="d-flex flex-column gap-2">
          <Input
            accept="image/*"
            type="file"
            onChange={(e) => setimage(e.target.files[0])}
          />
          <div>
            <Label>Title:</Label>
            <Input
              type="text"
              value={title}
              onChange={(e) => settitle(e.target.value)}
            />
          </div>
          <div>
            <Label>Price:</Label>
            <Input
              type="number"
              value={price}
              onChange={(e) => setprice(e.target.value)}
            />
          </div>
          {type.toLowerCase() == "membership" ? (
            <div>
              <div>
                <Label>Duration (Years):</Label>
                <Input
                  type="text"
                  value={duration}
                  onChange={(e) => {
                    if (e.target.value >= 0) {
                      setduration(e.target.value);
                    } else {
                      return;
                    }
                  }}
                />
              </div>
              <div className="mt-2">
                <Label>Discount (%):</Label>
                <Input
                  type="number"
                  value={discount}
                  onChange={(e) => {
                    if (e.target.value >= 0) {
                      setdiscount(e.target.value);
                    } else {
                      return;
                    }
                  }}
                />
              </div>
            </div>
          ) : (
            <>
              <div className="d-flex flex-column">
                <Label>Date:</Label>
                <DatePicker
                  className="form-control"
                  minDate={Date.now()}
                  value={date}
                  onChange={(e) => {
                    const finalDate = e.toDateString().split(" ");
                    finalDate.shift();
                    setdate(finalDate.join(" "));
                  }}
                />
              </div>
              <div className="d-flex flex-column">
                <Label>Total Bookings:</Label>
                <Input
                  type="number"
                  value={totalBookings}
                  onChange={(e) => settotalBookings(e.target.value)}
                />
              </div>
            </>
          )}
          <div>
            <Label>Description:</Label>
            <Input
              type="text"
              value={description}
              onChange={(e) => setdescription(e.target.value)}
            />
          </div>
          <div>
            <Label>Features:</Label>
            <Input
              placeholder="feature 1
feature2"
              type="textarea"
              value={features}
              onChange={handleFeaturesChange}
            />
          </div>
        </div>
      </ModalBody>
      <ModalFooter>
        <button
          className="btn bg-primary text-white"
          onClick={handleOnConfirm}
          disabled={apiCalled}
        >
          {data ? `Update ${type}` : `Add ${type}`}
        </button>{" "}
        <Button color="secondary" onClick={oncancel} disabled={apiCalled}>
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
}
