import React, { useState } from "react";
import { Button, Modal, ModalBody, ModalFooter, ModalHeader } from "reactstrap";

export default function ConfirmModal({ open, oncancel, booking }) {
  return (
    <div>
      <Modal isOpen={open} centered>
        <div className="px-4 py-3">
          <div className="d-flex justify-content-between w-100">
            <p className="h5">BOOKING DETAILS</p>
            <p className="h5" onClick={oncancel} style={{ cursor: "pointer" }}>
              x
            </p>
          </div>
          <div className="d-flex flex-column gap-3">
            <div>
              <label className="form-label">Booking ID:</label>
              <input readOnly className="form-control" value={booking?._id} />
            </div>

            <div>
              <label className="form-label">Name:</label>
              <input readOnly className="form-control" value={booking?.name} />
            </div>
            <div>
              <label className="form-label">Email:</label>
              <input readOnly className="form-control" value={booking?.email} />
            </div>
            <div>
              <label className="form-label">Phone:</label>
              <input readOnly className="form-control" value={booking?.phone} />
            </div>
            {/* <div>
              <label className="form-label">Number of Persons:</label>
              <input
                readOnly
                className="form-control"
                value={booking?.persons}
              />
            </div> */}
          </div>
        </div>
      </Modal>
    </div>
  );
}
