import React, { useState } from "react";
import {
  Button,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
} from "reactstrap";

export default function AddProductModal({ open, oncancel, onconfirm }) {
  const [name, setname] = useState("");
  const [price, setprice] = useState(0);
  const [quantity, setquantity] = useState(0);
  const [images, setimages] = useState([]);
  const [sizes, setsizes] = useState("");
  const [features, setfeatures] = useState("");
  const [apiCalled, setapiCalled] = useState(false);

  const handleOnConfirm = () => {
    if (name && images.length > 0 && price > 0 && quantity > 0) {
      setapiCalled(true);
      onconfirm({
        data: {
          name,
          price,
          quantity,
          createdAt: Date.now(),
          features: features != "" ? features.split("\n") : [],
          sizes: sizes != "" ? sizes.split("\n") : [],
          productImages: images,
        },
      });
    }
  };

  return (
    <Modal isOpen={open} centered>
      <ModalHeader>Add Product</ModalHeader>
      <ModalBody>
        <div className="d-flex flex-column gap-2">
          <Input
            accept="image/*"
            type="file"
            multiple
            onChange={(e) => setimages(e.target.files)}
          />
          <div>
            <Label>Product Name:</Label>
            <Input
              type="text"
              value={name}
              onChange={(e) => setname(e.target.value)}
            />
          </div>
          <div>
            <Label>Product Price:</Label>
            <Input
              type="number"
              value={price}
              onChange={(e) => setprice(e.target.value)}
            />
          </div>
          <div>
            <Label>Product Quantity:</Label>
            <Input
              type="number"
              value={quantity}
              onChange={(e) => setquantity(e.target.value)}
            />
          </div>
          <div>
            <Label>Sizes:</Label>
            <Input
              placeholder="Size 1
Size 2"
              type="textarea"
              value={sizes}
              onChange={(e) => setsizes(e.target.value)}
            />
          </div>
          <Label>Features:</Label>
          <Input
            placeholder="feature 1
feature2"
            type="textarea"
            value={features}
            onChange={(e) => setfeatures(e.target.value)}
          />
        </div>
      </ModalBody>
      <ModalFooter>
        <button
          className="btn bg-primary text-white"
          onClick={handleOnConfirm}
          disabled={apiCalled}
        >
          Create Product
        </button>{" "}
        <Button color="secondary" onClick={oncancel} disabled={apiCalled}>
          Cancel
        </Button>
      </ModalFooter>
    </Modal>
  );
}
