import { axiosClient } from "./axios";

export const bookEvent = async ({ data, id }) => {
  try {
    const res = await axiosClient.post(`/booking/${id}`, data);
    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getBookingsOfEvent = async ({ id }) => {
  try {
    const res = await axiosClient.get(`/booking/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
