import React, { useContext, useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { AuthContext } from "../contexts/userContext";
import { getBanner, updateBanner } from "../api/banner";
import UpdateBannerModal from "./modals/UpdateBannerModal";
import BGImg from "../assets/Images/product-banner.jpg";

export default function PageTop() {
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const location = useLocation();

  const [background, setbackground] = useState(BGImg);
  const [smBackground, setsmBackground] = useState(BGImg);
  const [loading, setloading] = useState(true);
  const [modalOpen, setmodalOpen] = useState(false);

  const { currentUser } = useContext(AuthContext);

  const navigate = useNavigate();

  const handleGetBackground = () => {
    setloading(true);
    getBanner({
      type:
        location.pathname.split("/")[1] == ""
          ? "home"
          : location.pathname.split("/")[1],
    })
      .then((res) => {
        setloading(false);
        res.image.map((img) => {
          const fileName = img.split("/")[img.split("/").length - 1];
          if (fileName.includes("_sm")) {
            setsmBackground(baseUrl + img);
          } else {
            setbackground(baseUrl + img);
          }
        });
      })
      .catch((err) => {
        setbackground(BGImg);
        setloading(false);
      });
  };

  const handleUpdateBanner = ({ image }) => {
    updateBanner({
      type:
        location.pathname.split("/")[1] == ""
          ? "home"
          : location.pathname.split("/")[1],
      image,
    })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {});
  };

  useEffect(() => {
    handleGetBackground();
  }, [location.pathname]);

  return (
    <div
      className="background-image position-relative"
      style={{
        // backgroundImage: `${background}`,
        // backgroundSize: "100% 100%",
        // backgroundPosition: "center",
        width: "100%",
        height: "400px",
        maxHeight: "400px",
      }}
    >
      <div
        className="position-absolute w-100 h-100 top-0 left-0"
        style={{ zIndex: 1 }}
      >
        <img
          src={background}
          className="d-none d-sm-block"
          style={{ width: "100%", height: "100%" }}
        />
        <img
          src={smBackground}
          className="d-block d-sm-none"
          style={{ width: "100%", height: "100%" }}
        />
      </div>
      <div
        className="d-flex flex-column align-items-center justify-content-end position-relative h-100 w-100"
        style={{ zIndex: 2 }}
      >
        <UpdateBannerModal
          open={modalOpen}
          oncancel={() => setmodalOpen(false)}
          onconfirm={handleUpdateBanner}
        />
        {currentUser?.isAdmin && (
          <button
            className="btn border-white bg-dark rounded-2 border-3 font-mont fw-bold mb-3 px-4 text-white"
            onClick={() => setmodalOpen(true)}
          >
            UPDATE BANNER
          </button>
        )}
        <button
          className="btn border-white rounded-0 border-3 font-mont fw-bold mb-3 px-4 text-white"
          onClick={() => navigate("/merch")}
        >
          SHOP NOW
        </button>
      </div>
    </div>
  );
}
