import React, { useContext, useState } from "react";
import HomeInstagram from "./HomeInstagram";
import HomeYoutube from "./HomeYoutube";
import SocialPostModal from "../modals/socialPost/SocialPostModal";
import { AuthContext } from "../../contexts/userContext";
import { createSocialPost } from "../../api/socialPost";

export default function SocialPosts({ socialPosts }) {
  const { currentUser } = useContext(AuthContext);

  const [addModalOpen, setaddModalOpen] = useState(false);
  const instaPosts = socialPosts.filter((post) => post.type == "instagram");
  const youtubePosts = socialPosts.filter((post) => post.type == "youtube");

  const handleAddPost = ({ data }) => {
    createSocialPost({ data })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {});
  };

  return (
    <div>
      <SocialPostModal
        open={addModalOpen}
        onconfirm={handleAddPost}
        oncancel={() => setaddModalOpen(false)}
      />
      {currentUser?.isAdmin && (
        <div className="text-end">
          <button
            className="btn bg-white btn-lg"
            onClick={() => setaddModalOpen(true)}
          >
            Add Social Post
          </button>
        </div>
      )}

      <div className="">
        <HomeInstagram socialPosts={instaPosts} />
      </div>
      <div className="mt-5 pt-5">
        <HomeYoutube socialPosts={youtubePosts} />
      </div>
    </div>
  );
}
