import React, { useContext, useState } from "react";
import "../../assets/css/Product_Card.css";
import { Link } from "react-router-dom";
import { AuthContext } from "../../contexts/userContext";
import SocialPostModal from "../modals/socialPost/SocialPostModal";
import { deleteSocialPost, updateSocialPost } from "../../api/socialPost";
import ConfirmModal from "../modals/ConfirmModal";

function Product_Card({ product }) {
  const { currentUser } = useContext(AuthContext);

  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  const [editModalOpen, seteditModalOpen] = useState(false);
  const [confirmModalOpen, setconfirmModalOpen] = useState(false);

  const handleUpdate = ({ data }) => {
    updateSocialPost({ data, id: product._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {});
  };

  const handleDelete = () => {
    deleteSocialPost({ id: product._id })
      .then((res) => {
        window.location.reload();
      })
      .catch((err) => {});
  };

  return (
    <div className="col-12 col-sm-6 col-lg-3 d-flex justify-content-center flex-column align-items-center position-relative mb-4">
      <SocialPostModal
        open={editModalOpen}
        onconfirm={handleUpdate}
        oncancel={() => seteditModalOpen(false)}
        post={product}
      />
      <ConfirmModal
        open={confirmModalOpen}
        oncancel={() => setconfirmModalOpen(false)}
        title={"CONFIRM DELETE"}
        description={"Are you sure you want to delete this post?"}
        onconfirm={handleDelete}
      />
      <Link to={product.url} target="_blank">
        <img
          className="product-image rounded"
          src={baseUrl + product.image}
        ></img>
      </Link>
      {currentUser?.isAdmin && (
        <div className="d-flex gap-3 flex-wrap justify-content-center justify-content-md-start mt-3">
          <div className="d-flex justify-content-center justify-content-md-start">
            <button
              className="btn bg-white text-dark"
              onClick={() => seteditModalOpen(true)}
            >
              EDIT
            </button>
          </div>
          <div className="d-flex justify-content-center justify-content-md-start">
            <button
              className="btn bg-danger text-white"
              onClick={() => setconfirmModalOpen(true)}
            >
              DELETE
            </button>
          </div>
        </div>
      )}
    </div>
  );
}

export default Product_Card;
