import React, { useContext } from "react";
import { AuthContext } from "../../contexts/userContext";

export default function MembershipCheckoutRight({ membership }) {
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";

  return (
    <div>
      <div className="mx-xl-3">
        <div className="d-flex flex-column  gap-5">
          <div className="d-flex flex-column gap-3">
            <div className="d-flex flex-column gap-3">
              <div className="d-flex flex-column">
                <p className="">Plan: {membership.title}</p>
                <p className=" text-wrap">
                  Description: {membership.description}
                </p>
              </div>
            </div>
            <div>
              <p className="text-secondary">INCLUDES</p>
              {membership.features.map((feature) => (
                <div className="d-flex align-items-center gap-2 mb-2">
                  <p
                    className="fw-bold m-0 rounded-circle bg-white"
                    style={{ padding: "3px" }}
                  ></p>
                  <p className="m-0 p-0 font-mont">{feature}</p>
                </div>
              ))}
            </div>
          </div>

          <div className="d-flex justify-content-between w-50">
            <p>Subtotal</p>
            <p>USD ${membership.price}</p>
          </div>
          <div className="d-flex justify-content-between w-50">
            <div className="d-flex flex-column">
              <p>Total</p>
              <p className="xsmall">Including Tax</p>
            </div>
            <p>USD ${membership.price}</p>
          </div>
        </div>
      </div>
    </div>
  );
}
