import React from "react";
import "../../assets/css/Membership.css";
import "../../assets/css/Merch.css";
import { useState } from "react";
import HomeMembershipCard from "./HomeMembershipCard";

function HomeMembership({ membership }) {
  return (
    <div className="membership-bg">
      <div>
        <div className="d-flex flex-column gap-5">
          {membership.map((m, i) => {
            return <HomeMembershipCard member={m} />;
          })}
        </div>
      </div>
    </div>
  );
}
export default HomeMembership;
