import React from "react";
import "../../assets/css/Event_Card.css";
import { useNavigate } from "react-router-dom";

function HomeEventsCard({ event }) {
  const baseUrl = process.env.REACT_APP_BASE_URL + "/";
  const navigate = useNavigate();

  return (
    <div className="mb-5 mb-sm-0 d-flex justify-content-center position-relative">
      <div
        onClick={() => navigate(`/event-book/${event._id}`)}
        className="rounded home-event-card"
        style={{
          cursor: "pointer",
          background: `url(${baseUrl + event.image})`,
          backgroundRepeat: "no-repeat",
          backgroundPosition: "center",
          backgroundSize: "cover",
        }}
      >
        <div
          className="position-absolute h-100 w-100"
          style={{ left: 0, background: "rgba(0,0,0,0.5)", zIndex: 1 }}
        ></div>
        <div
          className="d-flex align-items-center justify-content-center h-100 position-relative"
          style={{ zIndex: 2 }}
        >
          <div className="h-50 d-flex align-items-center flex-column justify-content-center text-center">
            <div>
              <h2 className="mb-2 text-light">{event.title}</h2>
            </div>
          </div>
        </div>
      </div>
      {/*
<img className="product-image-event rounded " src={event.image}></img>

      <h4 className="product-title-event ">{event.title}</h4>
      */}
    </div>
  );
}

export default HomeEventsCard;
