import { axiosClient } from "./axios";

export const getSocialPosts = async () => {
  try {
    const res = await axiosClient.get(`/socialPost`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const createSocialPost = async ({ data }) => {
  try {
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.post("/socialPost", data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const updateSocialPost = async ({ data, id }) => {
  try {
    console.log(id);
    axiosClient.defaults.headers = {
      "Content-Type": "multipart/form-data",
    };

    const res = await axiosClient.put(`/socialPost/${id}`, data);

    axiosClient.defaults.headers = {
      "Content-Type": "application/json",
      Accept: "application/json",
    };

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const deleteSocialPost = async ({ id }) => {
  try {
    const res = await axiosClient.delete(`/socialPost/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
