import Cookies from "js-cookie";
import { axiosClient } from "./axios";

export const checkLoggedIn = async () => {
  try {
    const response = await axiosClient.get(`/user/isLoggedIn`);
    return response.data;
  } catch (err) {
    throw err;
  }
};

export const register = async ({ username, email, password }) => {
  try {
    const response = await axiosClient.post(`/user/register`, {
      username,
      email,
      password,
    });

    return response.data;
  } catch (err) {
    throw err;
  }
};

export const login = async ({ email, password }) => {
  try {
    const response = await axiosClient.post(`/user/login`, {
      email,
      password,
    });

    return response.data;
  } catch (err) {
    throw err;
  }
};

export const logout = async () => {
  try {
    const response = await axiosClient.post(`/user/logout`);
    window.localStorage.setItem("user", null);
    return response.data;
  } catch (err) {
    throw err;
  }
};

export const getUser = async () => {
  try {
    const response = await axiosClient.get(`/user/admin`);
    return response.data;
  } catch (err) {
    throw err;
  }
};

export const updateUser = async ({
  deliveryAddress,
  username,
  oldPassword,
  newPassword,
}) => {
  try {
    const response = await axiosClient.put(`/user/single`, {
      deliveryAddress,
      username,
      oldPassword,
      newPassword,
    });
    return response.data;
  } catch (err) {
    throw err;
  }
};

export const updatePlan = async ({ planId }) => {
  try {
    const response = await axiosClient.post(`/user/plan/${planId}`);
    return response.data;
  } catch (err) {
    throw err;
  }
};
