import { axiosClient } from "./axios";

export const createPayment = async ({ data }) => {
  try {
    const res = await axiosClient.post("/payment/create-session", data);
    return res.data;
  } catch (err) {
    throw err;
  }
};

export const createMembershipOrder = async ({ data }) => {
  try {
    const res = await axiosClient.post(
      "/payment/create-membership-session",
      data
    );
    return res.data;
  } catch (err) {
    throw err;
  }
};

export const createBookingOrder = async ({ data }) => {
  try {
    const res = await axiosClient.post("/payment/create-booking-session", data);
    return res.data;
  } catch (err) {
    throw err;
  }
};

export const checkPaymentStatus = async ({ sessionId }) => {
  try {
    const res = await axiosClient.get(`/payment/status/${sessionId}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getPaymentHistory = async () => {
  try {
    const res = await axiosClient.get(`/payment/all`);

    return res.data;
  } catch (err) {
    throw err;
  }
};
