import React from "react";
import { useState } from "react";
import "../../assets/css/Merch.css";
import HomeInstagramCard from "./HomeInstagramCard";
import instagram_logo from "../../assets/Images/instagram_logo.png";
import { Link } from "react-router-dom";

function HomeInstagram({ socialPosts }) {
  return (
    <div>
      <div className=" mb-5 mt-5 d-block d-sm-flex text-center justify-content-center align-items-center gap-5 ">
        <p className="order-2 text-secondary m-0 p-0 h2">
          CHECK OUT OUR LATEST POST
        </p>
        <img
          style={{ height: "100px", width: "100" }}
          src={instagram_logo}
        ></img>
      </div>
      <div className="container-fluid">
        <div className="row">
          {socialPosts.map((m, i) => {
            return <HomeInstagramCard product={m} />;
          })}
        </div>
      </div>

      <button className="btn bg-white d-flex mt-5 justify-content-center align-items-center mx-auto">
        <img style={{ width: "30px" }} src={instagram_logo}></img>
        <Link
          className="text-dark text-decoration-none"
          to={
            "https://instagram.com/fishingtheatlantic?igshid=MzRlODBiNWFlZA=="
          }
          target="_blank"
        >
          <p className="m-0 p-0 h5">@FISHINGTHEATLANTIC</p>
        </Link>
      </button>
    </div>
  );
}

export default HomeInstagram;
