import { axiosClient } from "./axios";

export const getAllOrders = async () => {
  try {
    const res = await axiosClient.get(`/order`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const getOrder = async ({ id }) => {
  try {
    const res = await axiosClient.get(`/order/${id}`);

    return res.data;
  } catch (err) {
    throw err;
  }
};

export const createOrder = async ({
  products,
  total,
  deliveryAddress,
  paymentMethod,
}) => {
  try {
    const res = await axiosClient.post(`/order`, {
      paymentMethod,
      products,
      total,
      deliveryAddress,
    });

    return res.data;
  } catch (err) {
    throw err;
  }
};
