import React from "react";
import "../assets/css/Contact.css";
import email_icon from "../assets/Images/email_icon.png";
import phone_icon from "../assets/Images/phone_icon.png";
import location_icon from "../assets/Images/location_icon.png";

export default function Contact() {
  return (
    <div>
      <h3 className="title-contact text-center mb-5 pb-5">CONTACT US</h3>

      <div className="bg-secondary w-50 rounded-5 m-auto px-4 contact-main-div d-flex flex-column justify-content-center">
        <div>
          <h1 className="text-center mb-4 text-dark font-mont">
            Get in touch with us
          </h1>
          <div className="d-flex flex-column gap-4">
            <div className="d-flex justify-content-between gap-3 gap-md-5 flex-wrap flex-md-nowrap">
              <div className="w-100">
                <input
                  type="text"
                  className="contact-input border-0 form-control-lg w-100 font-russo"
                  id="name"
                  name="name"
                  placeholder="NAME:"
                  required
                />
              </div>
              <div className="w-100">
                <input
                  type="email"
                  className="contact-input border-0 form-control-lg w-100 font-russo"
                  id="email"
                  name="email"
                  placeholder="EMAIL:"
                  required
                />
              </div>
            </div>
            <textarea
              className="contact-input border-0 form-control-lg font-russo"
              id="message"
              name="message"
              rows="4"
              placeholder="MESSAGE:"
              required
            ></textarea>

            <button className="btn bg-white m-auto px-4">SEND</button>
          </div>
        </div>
      </div>

      <div className="text-center my-5">
        <img className="contact-img" src={email_icon}></img>
        <p className="contact-email-title">EMAIL ADDRESS</p>
        <p className="actual-email font-mont">
          fishingtheatlanticocean@gmail.com
        </p>
      </div>

      <div className="text-center my-5">
        <img className="contact-img" src={phone_icon}></img>
        <p className="contact-email-title">CALL US</p>
        <p className="actual-email font-mont">+1 (718) 310-8430</p>
      </div>

      <div className="text-center my-5">
        <img className="contact-img" src={location_icon}></img>
        <p className="contact-email-title">LOCATION</p>
        <p className="actual-email font-mont">Howard Beach</p>
      </div>
    </div>
  );
}
